import axios from 'axios'
import { Formik } from 'formik'
import React, { useState } from 'react'
import { BeatLoader } from 'react-spinners'
import Swal from 'sweetalert2'
import * as Yup from 'yup'
export default function Footerpage() {

    const REACT_APP_web_link=process.env.REACT_APP_web_link
   

    const ValidationSchema =
        Yup.object({
            name:Yup.string()
            .required('Please enter your name')
            .min(2,'minimum number of characters are 2'),
            email:Yup.string()
            .required('Please enter your email')
            .email('This is an email field'),
            phone:Yup.number('Phone number should be numbers')
            .required('Please enter your phone'),
            message:Yup.string()
            .required('Please enter your message'),
        })
    
    const [loading,setLoading] = useState(false);

  return (
    <Formik
    initialValues={{
        name:'',
        email:'',
        phone:'',
        message:'',
    }}
    validationSchema={ValidationSchema}
    onSubmit={((values)=>{

        setLoading(true)
        setTimeout(async() => {
            await axios.post(`${REACT_APP_web_link}api/receive_footer_message/`,{...values})
            .then((response)=>{

                Swal.fire({
                    icon:'success',
                    title:'Form submitted',
                    // text:JSON.stringify(values),
                    timer:5000,
                    timerProgressBar:true,
                    showDenyButton:false,
                    showConfirmButton:false,
                    showCancelButton:false
                }).then(()=>{
    
                    setTimeout(() => {
                        setLoading(false)
                        window.location.reload()
                    }, 200);
                })
            }).catch((e)=>{

                console.log(e)
                Swal.fire({
                    icon:'error',
                    title:'An error occurred, unable to send details',
                    // text:JSON.stringify(values),
                    timer:5000,
                    timerProgressBar:true,
                    showDenyButton:false,
                    showConfirmButton:false,
                    showCancelButton:false
                })

            })

        }, 1000);
        
    })}
    >
        {((props)=>(
            <div id='footernav' className='xs:px-[20px] xs:py-[15px] lg:px-[191px] lg:py-[85px] bg-black h-[779px] xs:mt-[90px] lg:mt-[233px] w-full text-white flex flex-col items-center justify-start'>
            
            <h2 className='text-center text-white text-3xl font-bold xs:mb-[20px] lg:mb-[40px]'>Send us a message</h2>

            <div className='xs:flex xs:flex-col lg:grid lg:grid-cols-2 gap-x-0 xs:gap-y-[14px] lg:gap-y-[32px] items-center justify-center'>

                <label className='text-white font-bold text-2xl'>Name</label>

                <div>
                <input onChange={props.handleChange('name')} onBlur={props.handleBlur('name')} required className='bg-white lg:px-[8px] text-black px-[8px] rounded-[12px] outline-none h-[40px] w-[300px]'/>
                <p className='mt-4 text-red-500 font-bold'>{props.touched.name && props.errors.name}</p>
                </div>

                <label className='text-white font-bold text-2xl'>Email</label>

                <div>
                <input onChange={props.handleChange('email')} onBlur={props.handleBlur('email')} required className='bg-white lg:px-[8px] text-black px-[8px] rounded-[12px] outline-none h-[40px] w-[300px]'/>
                <p className='mt-4 text-red-500 font-bold'>{props.touched.email && props.errors.email}</p>
                </div>


                <label className='text-white font-bold text-2xl'>Phone</label>
                <div>
                <input onChange={props.handleChange('phone')} onBlur={props.handleBlur('phone')}  required className='bg-white lg:px-[8px] text-black px-[8px] rounded-[12px] outline-none h-[40px] w-[300px]'/>
                <p className='mt-4 text-red-500 font-bold'>{props.touched.phone && props.errors.phone}</p>
                
                </div>


                <label className='text-white font-bold text-2xl'>Message</label>
                <div>
                <textarea onChange={props.handleChange('message')} onBlur={props.handleBlur('message')} required className='bg-white text-black px-[8px] rounded-[12px] outline-none w-[300px] lg:py-[3px] lg:px-[8px] h-[200px] resize-none'/>
                <p className='mt-4 text-red-500 font-bold'>{props.touched.message && props.errors.message}</p>

                </div>

                <div>

                </div>

                <div className='w-full flex flex-row items-center justify-center'>
                {
                loading?
                <BeatLoader
                color='#3EBF53'
                size={32} 
                />
                :
                <button onClick={props.handleSubmit} className='w-full text-white bg-new_green h-[53px] rounded-[12px] shadow-md'>
                Send
                </button> 
                }
                </div>


                </div>


            </div>
        ))}
        


       
      
    </Formik>
  )
}
