import axios from 'axios';
import React, { useEffect, useState } from 'react'

import { PaystackConsumer } from 'react-paystack';

import { useLocation, useNavigate } from 'react-router-dom';
import { ClipLoader } from 'react-spinners';
import Swal from 'sweetalert2'
import Cookies from 'js-cookie';
const {REACT_APP_paystack_public_key} = process.env



export default function Vote_paystack() {

    const location = useLocation();
    const { user,data } = location.state || {};
    const navigate = useNavigate()
    
    const config = {
        reference: (new Date()).getTime().toString(),
        email: "user@example.com",
        currency:'GHS',
        amount: 100 * parseFloat(data?.amount_to_pay), //Amount is in the country's lowest currency. E.g Kobo, so 20000 kobo = N200
        publicKey: REACT_APP_paystack_public_key,
    };

//   const [user_active,setUseractive]=useState(null)
//   useEffect(()=>{
//       const interval = setInterval(() => {
//           const cookie_user =Cookies.get('user')
//           if(cookie_user) {
//             Cookies.remove('user')
//             window.location.href='/'
//           }
//       }, 1000);

//       return ()=>clearInterval(interval)

//   },[user_active,setUseractive])


    // const REACT_APP_web_link = process.env.REACT_APP_web_link


    config.email = (user.email)
    config.phone = (user.phone)
    config.full_name = (user.name)

    
    // you can call this function anything
    const handleSuccess = (reference) => {
      // Implementation for whatever you want to do with reference and after success call.

      setLoading(true)

        setTimeout(async() => {
          
          
          await axios.post(`${process.env.REACT_APP_web_votes}`,{...data},{
            headers:{
                Authorization:`Bearer ${user?.access}`
              }
          })
          .then((response)=>{

            // localStorage.removeItem('user_data_to_sent')

            Swal.fire({
              icon:'success',
              title:'Form submitted',
              // text:JSON.stringify({...user,...reference}),
              timer:5000,
              timerProgressBar:false,
              showDenyButton:false,
              showConfirmButton:true,
              showCancelButton:false
          }).then((response)=>{
            navigate('/event_home')
          })
            
          })
          .catch((e)=>{
            console.log(e)
            Swal.fire({
              icon:'error',
              title:'Form Unable to submit form',
              text:'please try again later or contact support voteify@gmail.com',
              // text:JSON.stringify(e),
              // timer:5000,
              // timerProgressBar:false,
              showDenyButton:false,
              showConfirmButton:true,
              showCancelButton:false
          }).then((response)=>{
            window.location.href='/'
          })
          })
          .finally(()=>setLoading(false))
            // setLoading(false)
        }, 2000);

    };
    
    // you can call this function anything
    const handleClose = () => {
      // implementation for  whatever you want to do when the Paystack dialog closed.
      console.log('closed')

      Swal.fire({
        icon:'error',
        title:'Form not Submitted',
        // text:JSON.stringify({...user,...reference}),
        timer:5000,
        timerProgressBar:true,
        showDenyButton:false,
        showConfirmButton:false,
        showCancelButton:false
    })

    }


    const componentProps = {
        ...config,
        text: 'Paystack Payment',
        onSuccess: (reference) => handleSuccess(reference),
        onClose: handleClose
    };

    const [loading,setLoading] = useState(false);
    
  return (
    <div className='xs:w-full md:w-full shadow-lg  h-screen  flex flex-col items-center justify-center'>

        
        {
            loading?
            <ClipLoader size={52} color='#3EBF53' />
            :
        <div className='w-[200px] h-[100px] rounded-[12px] flex flex-row items-center justify-center text-white bg-new_green font-bold'>
        <PaystackConsumer {...componentProps} >
          {({initializePayment}) => <button className='w-[200px] h-[100px] rounded-[12px] text-xl text-white bg-new_green font-bold' onClick={
            () => {initializePayment(handleSuccess, handleClose)}
            }>Finalize Vote Payment</button>}
        </PaystackConsumer>

        </div>
        }

        <p className='w-full font-bold text-center mt-[22px] text-md'>Non Refundable!</p>

      
    </div>
  )
}
