import { Formik } from 'formik'
import React, { useState } from 'react'
import { useLocation, useNavigate } from 'react-router-dom'
import { BeatLoader } from 'react-spinners'
import * as Yup from 'yup'
import axios from 'axios'
import Swal from 'sweetalert2'
import Resizer from "react-image-file-resizer";

export default function Createcontestant() {

    const navigate = useNavigate()
    
    const base64ToFile = (base64String, filename) => {
      let arr = base64String.split(',');
      let mime = arr[0].match(/:(.*?);/)[1];
      let bstr = atob(arr[1]);
      let n = bstr.length;
      let u8arr = new Uint8Array(n);
    
      while (n--) {
        u8arr[n] = bstr.charCodeAt(n);
      }
    
      return new File([u8arr], filename, { type: mime });
    };

    
    const ContestantValidationSchema = Yup.object({
        name_of_contestant:Yup.string()
        .min(2,'Minimum number of characters not met')
        .required('Please enter the name of the event'),
        contestant_1: Yup.mixed()
        .required('Please upload a first picture for your contestant'),
        contestant_2: Yup.mixed()
        .required('Please upload a second picture for your contestant'),
        contestant_3: Yup.mixed()
        .required('Please upload a third picture for your contestant'),
        contestant_4: Yup.mixed()
        .required('Please upload a fourth picture for your contestant'),
    })
    const location = useLocation()
    const [preview1,setPreview1]=useState(null)
    const [preview2,setPreview2]=useState(null)
    const [preview3,setPreview3]=useState(null)
    const [preview4,setPreview4]=useState(null)
    const [ loading,setLoading ] = useState(false);
    const { event,user } = location.state || {}
  return (
    <div className='flex-wrap flex xs:flex-col lg:flex-row items-center justify-between xs:px-[20px] md:px-[40px]  lg:px-[191px] w-full xs:mt-[59px] lg:mt-[20px] mb-[40px]'>

        <Formik
        validationSchema={ContestantValidationSchema}
        initialValues={{
            name_of_contestant:'',
            contestant_1:'',
            contestant_2:'',
            contestant_3:'',
            contestant_4:'',
        }}
        onSubmit={((values)=>{

            setLoading(!loading)

            setTimeout(async() => {
                // localhost/event/contestants/data/
                const formData = new FormData();
                formData.append('uploaded_files',values.contestant_1)
                formData.append('uploaded_files',values.contestant_2)
                formData.append('uploaded_files',values.contestant_3)
                formData.append('uploaded_files',values.contestant_4)
                formData.append('event',event.id)
                formData.append('name',values.name_of_contestant)

                
                await axios.post(`${process.env.REACT_APP_web_link_event}contestants/data/`, formData, {
                headers: {
                    'Authorization': `Bearer ${user?.access}`,
                    'Content-Type': 'multipart/form-data', // This is important for FormData
                }
                }).then((response)=>{
                    Swal.fire({
                      icon:'success',
                      title:'Contestant Info Submitted',
                      timer:3000,
                      timerProgressBar:true,
                      showConfirmButton:false
                    }).then(()=>{
                      navigate(-1)
                    })
                  }).catch((e)=>{
                    console.log(e)
                    Swal.fire({
                      icon:'error',
                      title:'An error occurred, please try again later',
                      timer:3000,
                      timerProgressBar:true,
                      showConfirmButton:false
                    })
                  }).finally(()=>{
                    setLoading(false)
                  })
            }, 1000);
        })}
        >
            {((props)=>(
                <div className='flex flex-col flex-wrap xs:w-[100%] md:w-[65%] lg:w-1/3 shadow-xl h-auto py-[20px] px-[10px] rounded-xl'>

                <h2 className='w-full text-center font-bold text-xl'>Add Contestant Information </h2>
        
                <input
                placeholder='name'
                onChange={props.handleChange('name_of_contestant')} onBlur={props.handleBlur('name_of_contestant')}
                className='lg:-translate-x-[8px] px-[8px] py-[4px] outline-none mx-auto bg-input_field mt-[42px] xs:w-full lg:w-[319px] h-[63px] rounded-[12px] shadow-lg'
                />
                <p className=' text-center w-full mt-2 text-red-500 font-bold'>{props.touched.name_of_contestant && props.errors.name_of_contestant}</p>

                    <input
                     id='1' 
                    placeholder='picture'
                    accept='image/*'
                    onChange={(event) => {

                        Resizer.imageFileResizer(
                            event.target.files[0],
                            150,
                            150,
                            "JPEG",
                            100,
                            0,
                            (uri) => {
                              // console.log(event.target.files[0].name);
                              const file = base64ToFile(uri, `${event.target.files[0].name}`);
                              const previewUrl = URL.createObjectURL(file);
                              setPreview1(previewUrl);
                              // props.setFieldValue('event_picture', file);
                              props.setFieldValue('contestant_1', event.target.files[0]);
                              // props.setFieldValue('event_picture', uri);
                            },
                            "base64",
                            100,
                            100
                          );

                        
                      }}
                    onBlur={props.handleBlur('contestant_1')}
                     className='hidden'
                     type='file'
                     />

                    <input
                     id='2' 
                    placeholder='picture'
                    accept='image/*'
                    onChange={(event) => {

                        Resizer.imageFileResizer(
                            event.target.files[0],
                            150,
                            150,
                            "JPEG",
                            100,
                            0,
                            (uri) => {
                              // console.log(event.target.files[0].name);
                              const file = base64ToFile(uri, `${event.target.files[0].name}`);
                              const previewUrl = URL.createObjectURL(file);
                              setPreview2(previewUrl);
                              // props.setFieldValue('event_picture', file);
                              props.setFieldValue('contestant_2', event.target.files[0]);
                              // props.setFieldValue('event_picture', uri);
                            },
                            "base64",
                            100,
                            100
                          );

                        
                      }}
                    onBlur={props.handleBlur('contestant_2')}
                     className='hidden'
                     type='file'
                     />

                
                
                    <input
                     id='3' 
                    placeholder='picture'
                    accept='image/*'
                    onChange={(event) => {

                        Resizer.imageFileResizer(
                            event.target.files[0],
                            150,
                            150,
                            "JPEG",
                            100,
                            0,
                            (uri) => {
                              // console.log(event.target.files[0].name);
                              const file = base64ToFile(uri, `${event.target.files[0].name}`);
                              const previewUrl = URL.createObjectURL(file);
                              setPreview3(previewUrl);
                              // props.setFieldValue('event_picture', file);
                              props.setFieldValue('contestant_3', event.target.files[0]);
                              // props.setFieldValue('event_picture', uri);
                            },
                            "base64",
                            100,
                            100
                          );

                        
                      }}
                    onBlur={props.handleBlur('contestant_3')}
                     className='hidden'
                     type='file'
                     />

                    <input
                     id='4' 
                    placeholder='picture'
                    accept='image/*'
                    onChange={(event) => {

                        Resizer.imageFileResizer(
                            event.target.files[0],
                            150,
                            150,
                            "JPEG",
                            100,
                            0,
                            (uri) => {
                              // console.log(event.target.files[0].name);
                              const file = base64ToFile(uri, `${event.target.files[0].name}`);
                              const previewUrl = URL.createObjectURL(file);
                              setPreview4(previewUrl);
                              // props.setFieldValue('event_picture', file);
                              props.setFieldValue('contestant_4', event.target.files[0]);
                              // props.setFieldValue('event_picture', uri);
                            },
                            "base64",
                            100,
                            100
                          );

                        
                      }}
                    onBlur={props.handleBlur('contestant_4')}
                     className='hidden'
                     type='file'
                     />

               
                <p className='text-red-500 font-bold mx-1 mt-3 mb-1 text-center'>{props.touched.contestant_1 && props.errors.contestant_1}</p>
                <p className='text-red-500 font-bold mx-1 my-1 text-center'>{props.touched.contestant_2 && props.errors.contestant_2}</p>
                <p className='text-red-500 font-bold mx-1 my-1 text-center'>{props.touched.contestant_3 && props.errors.contestant_3}</p>
                <p className='text-red-500 font-bold mx-1 my-1 text-center'>{props.touched.contestant_4 && props.errors.contestant_4}</p>

                <div className='w-full flex gap-y-4 gap-x-4 flex-row flex-wrap mt-[20px] px-[14px]'>

                {preview1 &&
                    <div className='cursor-pointer w-[152px] h-[161px] rounded-lg shadow-lg'>
                    <label htmlFor='1' className='cursor-pointer w-[152px] h-[161px] bg-gray-300 rounded-lg shadow-lg'>
                     <img 
                     src={preview1}
                     alt='event'
                     className='w-[152px] h-[161px] rounded-lg shadow-lg'
                     />
                     </label>
                     </div>
                     }

                {preview2 &&
                    <div className='cursor-pointer w-[152px] h-[161px] rounded-lg shadow-lg'>
                    <label htmlFor='2' className='cursor-pointer w-[152px] h-[161px] bg-gray-300 rounded-lg shadow-lg'>
                     <img 
                     src={preview2}
                     alt='event'
                     className='w-[152px] h-[161px] rounded-lg shadow-lg'
                     />
                     </label>
                     </div>
                     }

                    {preview3 &&
                    <div className='cursor-pointer w-[152px] h-[161px] rounded-lg shadow-lg'>
                    <label htmlFor='3' className='cursor-pointer w-[152px] h-[161px] bg-gray-300 rounded-lg shadow-lg'>
                     <img 
                     src={preview3}
                     alt='event'
                     className='w-[152px] h-[161px] rounded-lg shadow-lg'
                     />
                     </label>
                     </div>
                     }

                    {preview4 &&
                    <div className='cursor-pointer w-[152px] h-[161px] rounded-lg shadow-lg'>
                    <label htmlFor='4' className='cursor-pointer w-[152px] h-[161px] bg-gray-300 rounded-lg shadow-lg'>
                     <img 
                     src={preview4}
                     alt='event'
                     className='w-[152px] h-[161px] rounded-lg shadow-lg'
                     />
                     </label>
                     </div>
                     }


                {
                !preview1&&
                <label htmlFor='1'  className={`cursor-pointer w-[152px] h-[161px] bg-gray-300 rounded-lg shadow-lg`}>
                </label>
                }
                

                { !preview2 &&<label htmlFor='2' className={`cursor-pointer w-[152px] h-[161px] bg-gray-300 rounded-lg shadow-lg`}>
                </label>}
                
                { !preview3 &&<label htmlFor='3' className={`cursor-pointer w-[152px] h-[161px] bg-gray-300 rounded-lg shadow-lg`}>
                </label>}

                { !preview4 &&<label htmlFor='4' className={`cursor-pointer w-[152px] h-[161px] bg-gray-300 rounded-lg shadow-lg`}>
                </label>}
                

                </div>

                <div className='w-full flex flex-row justify-center items-center'>

                {
                loading?
                <BeatLoader
                color='#3EBF53'
                size={32} 
                className='mt-6'
                />
                :
                <button onClick={props.handleSubmit} className='my-8 xs:mt-[40px] lg:mt-[34px] font-bold text-white bg-new_green w-[186px] h-[53px] rounded-[12px] shadow-md'>
            Add Contestant Info
        </button> 
                        }
                
                </div>

                </div>
            ))}


        </Formik>

        <img src={`${process.env.REACT_APP_link}${event?.event_picture}`}
        alt='event'
        width={'540px'}
        height={'540px'}
        className='object-contain rounded-xl shadow-2xl xs:my-16 md:my-10 lg:0-my'
        />
    </div>
  )
}
