import { Formik } from 'formik'
import React, { useState } from 'react'
import { useLocation, useNavigate, useParams } from 'react-router-dom'
import * as Yup from 'yup';
import Swal from 'sweetalert2';
import Cookies from 'js-cookie';
import { BeatLoader, ClipLoader } from 'react-spinners';
import axios from 'axios';
import Updatecontestant from './update-contestant';

export default function ShowUpdateContestant() {
    const location = useLocation()
    const navigate = useNavigate()
    const { id } = useParams()
    const {user,event,contestant,primeImage,position} = location.state || {}
    const [settings_on,setSettings_on] = useState(false);
    const [ deletescreen,setDeleteScreen ] = useState(false);
    const [ loader,setLoader ] = useState(false);
    const [ primeloader,setPrimeloader ] = useState(false);

    const updateImgStatus=(id)=>{
        //  contestants/select_prime_contestant_image/<str:id>/ ::contestant
        // REACT_APP_web_select_prime_contestant_image = http://localhost/event/contestants/select_prime_contestant_image/
        setPrimeloader(!primeloader)
        setTimeout(async() => {
            
        await axios.put(`${process.env.REACT_APP_web_select_prime_contestant_image}${id}/`,{contestant:contestant.id}, {
            headers: {
                'Authorization': `Bearer ${user?.access}`,
                'Content-Type': 'multipart/form-data', // This is important for FormData
            }
            })
        .then((response)=>{
            Swal.fire({
                'icon':'success',
                title:'Cover Picture Updated'
            }).then(()=>{
                navigate(-1)
            })
        }).catch((e)=>{
            Swal.fire({
                icon:'error',
                title:'Unable to update cover picture at this time'
            }).then(()=>{
                window.location.reload()
            })
        }).finally(()=>setPrimeloader(false))

        }, 1000);
    }

    const PasswordValidationSchema = Yup.object({
        password: Yup.string()
            .matches(/[A-Z]/, 'Password must contain at least one uppercase letter')
            .matches(/[a-z]/, 'Password must contain at least one lowercase letter')
            .matches(/\d/, 'Password must contain at least one number')
            .matches(/[@$!%*?&#]/, 'Password must contain at least one special character')
            .min(8, 'Password must be at least 8 characters long')
            .required('Password is required'),
    })

    const {REACT_APP_web_contestants} = process.env

  return (
    <div className='realtive flex flex-col xs:px-[20px] md:px-[40px]  lg:px-[191px] w-full xs:mt-[59px] lg:mt-[20px]'>
        <h2 className='text-blue-700 font-bold border-t-4 border-red-500  border-b-4 w-auto max-w-[150px] xs:text-center md:text-start text-2xl xs:mb-[30px] md:mb-[40px] capitalize'>{contestant?.name}</h2>

        
        

        <div className='z-30 realtive flex flex-row items-center justify-center'>

        <svg xmlns="http://www.w3.org/2000/svg" onClick={()=>setSettings_on(!settings_on)}  fill="none" viewBox="0 0 24 24" strokeWidth={1.5} stroke="currentColor" className={`absolute right-[60px] top-[120px] size-9 ${!settings_on?'text-black':'text-red-500'} duration-200 ease-in-out cursor-pointer`}>
        <path strokeLinecap="round" strokeLinejoin="round" d="m14.74 9-.346 9m-4.788 0L9.26 9m9.968-3.21c.342.052.682.107 1.022.166m-1.022-.165L18.16 19.673a2.25 2.25 0 0 1-2.244 2.077H8.084a2.25 2.25 0 0 1-2.244-2.077L4.772 5.79m14.456 0a48.108 48.108 0 0 0-3.478-.397m-12 .562c.34-.059.68-.114 1.022-.165m0 0a48.11 48.11 0 0 1 3.478-.397m7.5 0v-.916c0-1.18-.91-2.164-2.09-2.201a51.964 51.964 0 0 0-3.32 0c-1.18.037-2.09 1.022-2.09 2.201v.916m7.5 0a48.667 48.667 0 0 0-7.5 0" />
        </svg>

        {
        settings_on &&<div onClick={()=>{
        setSettings_on(false)
        setDeleteScreen(false)
        }} className='overflow-y-clip cursor-pointer w-full h-screen absolute top-0 left-0 z-40 bg-black opacity-60'></div>}

        {settings_on&&
        <Formik
        validationSchema={PasswordValidationSchema}
        initialValues={{
        password:''
        }}
        onSubmit={(values)=>{

        setLoader(true)
        setTimeout(async() => {
        
        await axios.delete(`${REACT_APP_web_contestants}${id}/?user=${user.id}&password=${values.password}`,{
            headers:{
                Authorization:`Bearer ${user?.access}`
            }
        })
        .then((response)=>{  
            Swal.fire({
            icon:'success',
            title:`Contestant Info Deleted`,
            showConfirmButton:true,
            }).then((response)=>{
            window.location.href='/'
            })
            
        })
        .catch((e)=>{
            console.log(e)
            Swal.fire({
            icon:'error',
            title:`Failed to delete event`,
            text:`Double check your entered password or try again later`,
            // timer:5000,
            showConfirmButton:true
            })
        }).finally(()=>setLoader(false))
        }, 1000);
        }}
        >
        {((props)=>(

        <div className='z-50 absolute xs:top-64 xs:left-9 lg:top-1/3 lg:left-1/3 transition-all duration-300 ease-in-out xs:w-[80%] md:w-1/3 h-[340px] bg-white rounded-[12px] shadow-2xl py-4 px-[24px] flex flex-col items-center justify-between'>

        <h2 className='text-red-500 font-bold text-2xl'>Permenently Delete Contestant Info</h2>
        <h2 className='text-center text-gray-600 px-[10px]'>Once the contestant info is deleted it cannot be recovered. Are you sure you want to delete it?</h2>


        <input 
        className='outline-none px-[10px] py-[8px] bg-gray-200 shadow-lg rounded-lg w-full my-3'
        type='password'
        placeholder='enter password here'
        value={props.values.password}
        onChange={props.handleChange('password')}
        onBlur={props.handleBlur('password')}
        />

        <p className='w-full text-center text-red-500 my-2'>{props.touched.password && props.errors.password}</p>
        <div onClick={props.handleSubmit} className='w-full flex flex-row items-center justify-between pl-[40px]'>


        {loader ?
        <BeatLoader
        color='red'
        size={22}
        />: <button className='-translate-x-[25%] cursor-pointer text-white px-4 py-2 rounded-[12px] bg-red-500 font-bold text-xl'>Yes I am</button>}

        <p onClick={()=>{
        setSettings_on(!settings_on)
        setDeleteScreen(!deletescreen)
        }} className='-translate-x-[25%] cursor-pointer text-white px-4 py-2 rounded-[12px] bg-gray-500 font-bold text-xl'>Cancel</p>

        </div>

        </div>
        ))}

        </Formik>
        }

        <img
          
          src={`${process.env.REACT_APP_link}${primeImage?.contestantdata_images}`}
          alt={`contestant info`}
          className={`cursor-pointer transition-all duration-300 ease-in-out  bg-empty_event_card lg:w-[660px] lg:h-[520px] md:w-[550px] md:h-[480px] xs:w-[330px] xs:mt-6 md:mt-0 xs:h-[330px] rounded-[12px] shadow-xl flex flex-row items-center justify-center mr-[40px] mb-[50px]`}
          onClick={()=>window.open(`${process.env.REACT_APP_link}${primeImage?.contestantdata_images}`,'_blank')}
          />
        </div>

        <div className='w-full text-center mt-3 mb-7 flex flex-row items-center justify-center'>
            <p className='font-bold text-xl border-t-4 border-red-500 border-b-4 w-52'>{contestant?.votes} Votes Recorded</p>

            <p className='capitalize border-t-4 border-blue-500 border-b-4 font-bold text-xl w-52'>{position} place</p>
            </div>

        <button className='xs:mt-[4px] font-bold text-white bg-new_green xs:w-[90%] ml-2 lg:w-[280px] h-[53px] rounded-[12px] shadow-md mb-[20px]'>
        Refer Contestant Info
        </button> 

        <div className='w-full px-8 mt-12 mb-16'>

            <h2 className='w-full text-center text-blue-500 font-bold underline text-2xl'>Pick Cover Picture</h2>

        { primeloader&&
            <div className='w-full flex items-center justify-center'>
                <ClipLoader
        color='#3EBF53'
        size={32}
        />
        </div>
        }

        <div className='w-full flex gap-y-4 gap-x-4 flex-row flex-wrap mt-[20px] px-[14px]'>
        <img 
        src={`${process.env.REACT_APP_link}${contestant?.contestant_images[0]?.contestantdata_images}`}
        alt='event'
        onClick={()=>updateImgStatus(contestant?.contestant_images[0]?.id)}
        className={`w-[152px] h-[161px] rounded-lg w-lg cursor-pointer transition-all duration-300 ease-in-out hover:rotate-12 mt-6 mb-4`}
        />
        <img 
        src={`${process.env.REACT_APP_link}${contestant?.contestant_images[1]?.contestantdata_images}`}
        alt='event'
        onClick={()=>updateImgStatus(contestant?.contestant_images[1]?.id)}
        className={`w-[152px] h-[161px] rounded-lg  shadow-lg  cursor-pointer transition-all duration-300 ease-in-out hover:rotate-12 mt-6 mb-4`}
        />
        <img 
        src={`${process.env.REACT_APP_link}${contestant?.contestant_images[2]?.contestantdata_images}`}
        alt='event'
        onClick={()=>updateImgStatus(contestant?.contestant_images[2]?.id)}
        className={`w-[152px] h-[161px] rounded-lg  shadow-lg  cursor-pointer transition-all duration-300 ease-in-out hover:rotate-12 mt-6 mb-4`}
        />
        <img 
        src={`${process.env.REACT_APP_link}${contestant?.contestant_images[3]?.contestantdata_images}`}
        alt='event'
        onClick={()=>updateImgStatus(contestant?.contestant_images[3]?.id)}
        className={`w-[152px] h-[161px] rounded-lg  shadow-lg  cursor-pointer transition-all duration-300 ease-in-out hover:rotate-12 mt-6 mb-4`}
        />
        </div>

        </div>

        <div className='w-full'>
        <Updatecontestant
        user={user}
        event={event}
        contestant={contestant}
        />
        </div>
    </div>
  )
}
