import axios from 'axios'
import { Formik } from 'formik'
import React, { useState } from 'react'
import { Link,useNavigate } from 'react-router-dom'
import { BeatLoader } from 'react-spinners'
import Swal from 'sweetalert2'
import * as Yup from 'yup'

import Cookies from 'js-cookie'

export default function Event_organizer_signin({loggedinuser,setLoggedinuser,setEvent_signup_user}) {
    const [loading,setLoading]=useState('')
    const REACT_APP_web_login = process.env.REACT_APP_web_login

    const navigate = useNavigate()
    const EventOrganizationValidationSchema = Yup.object({
        email:Yup.string()
        .email('Invalid email address')
        .required('Email is required'),
        password: Yup.string()
            .matches(/[A-Z]/, 'Password must contain at least one uppercase letter')
            .matches(/[a-z]/, 'Password must contain at least one lowercase letter')
            .matches(/\d/, 'Password must contain at least one number')
            .matches(/[@$!%*?&#]/, 'Password must contain at least one special character')
            .min(8, 'Password must be at least 8 characters long')
            .required('Password is required'),
        
    })
  return (
    <div className='xs:py-[32px] lg:py-[32px] w-full xs:px-[40px] lg:px-[191px] flex xs:flex-col lg:flex-row items-center justify-center h-full'>

        <img

        src={require('../assert/event_org_signin.png')}
        alt='event_org_signin.png'
        width={'539px'}
        height={'545px'}
        className='cursor-pointer'
        />

        <Formik
        validationSchema={EventOrganizationValidationSchema}
        initialValues={{
            email:'',
            password:'',
        }}
        onSubmit={((values)=>{
            
        setLoading(true)
        setTimeout(async() => {
            setLoading(false)
            
            await axios.post(REACT_APP_web_login,values)
            .then((response)=>{

                Cookies.set('user', JSON.stringify(response.data), { expires: 1, secure: true });
                setLoggedinuser(true)
                Swal.fire({
                    icon:'success',
                    title:'Login Successful',
                    // text:JSON.stringify(response.data),
                    timer:500,
                    timerProgressBar:true,
                    showDenyButton:false,
                    showConfirmButton:false,
                    showCancelButton:false
                }).then(()=>{
    
                        setEvent_signup_user(true)
                        navigate('/event_home')
                        // console.log(response)
                })

            }).catch((e)=>{
                Swal.fire({
                  icon:'error',
                  title:'Form Unable to submit form please try again',
                  // text:JSON.stringify(e),
                  // timer:5000,
                  // timerProgressBar:false,
                  showDenyButton:false,
                  showConfirmButton:true,
                  showCancelButton:false
              })
              })
              .finally(()=>setLoading(false))

        }, 1000);


        })}
        >
            {((props)=>(
                <div className='flex flex-col items-start justify-start xs:my-[40px] lg:my-0 xs:ml-[20px] xs:mr-[20px] lg:mr-0 lg:ml-[138px] xs:px-[20px] lg:px-[0px] xs:h-[auto] lg:h-auto xs:w-full lg:w-[418px] h-full shadow-lg rounded-[12px]'>

                    <h2 className='text-center w-full font-bold text-2xl my-4'>Sign in as an organizer</h2>

                    <input
                    placeholder='email'
                    type='email'
                     onChange={props.handleChange('email')} onBlur={props.handleBlur('email')}
                     className='px-[8px] py-[4px] outline-none mx-auto bg-input_field mt-[42px] xs:w-full lg:w-[319px] h-[63px] rounded-[12px] shadow-lg'
                     />
                     <p className=' text-center w-full mt-2 text-red-500 font-bold'>{props.touched.email && props.errors.email}</p>
                     <input
                     type='password'
                     placeholder='password'
                      onChange={props.handleChange('password')} onBlur={props.handleBlur('password')}
                     className='px-[8px] py-[4px] outline-none mx-auto bg-input_field mt-[22px] xs:w-full lg:w-[319px] h-[63px] rounded-[12px] shadow-lg'
                     />
                     <p className=' text-center w-full mt-2 text-red-500 font-bold'>{props.touched.password && props.errors.password}</p>


                     <Link to={'/forgotten_password'} className='w-full mx-auto my-4 pr-[54px]'>
                    <p className='text-md text-gray-400 text-right'>Forgotten Password?</p>
                    </Link>
                    

                    <div className='w-full flex flex-row items-center justify-center'>
                        {
                        loading?
                        <BeatLoader
                        color='#3EBF53'
                        size={32} 
                        />
                        :
                        <button onClick={props.handleSubmit} className='xs:mt-[40px] lg:mt-[34px] font-bold text-white bg-new_green w-[186px] h-[53px] rounded-[12px] shadow-md'>
                        Signin
                        </button> 
                        }

                        </div>

                        <Link to={'/become-an-event-supervisor'} className='mx-auto my-4'>
                        <p className='font-bold text-center text-xl'>Not an organizer? Sign up</p>
                        </Link>
                </div>
            ))}
        </Formik>
    </div>
  )
}
