import axios from 'axios'
import React, { useEffect, useState } from 'react'
import { useLocation, useNavigate, useParams } from 'react-router-dom'
import { Formik } from 'formik'
import * as Yup from 'yup';
import Swal from 'sweetalert2';
import Cookies from 'js-cookie';
import { BeatLoader, ClipLoader } from 'react-spinners';
export default function Singleevent() {
    const { id } = useParams()
    const location = useLocation()
    const { event } = location.state || {}
    const [ deletescreen,setDeleteScreen ] = useState(false);
    const [ loader,setLoader ] = useState(false);
    const navigate = useNavigate()
    const [ userdata,setUserdata ] = useState('')

    useEffect(()=>{
        const user = Cookies.get('user')
        setUserdata(JSON.parse(user))
      },[])

    const PasswordValidationSchema = Yup.object({
        password: Yup.string()
            .matches(/[A-Z]/, 'Password must contain at least one uppercase letter')
            .matches(/[a-z]/, 'Password must contain at least one lowercase letter')
            .matches(/\d/, 'Password must contain at least one number')
            .matches(/[@$!%*?&#]/, 'Password must contain at least one special character')
            .min(8, 'Password must be at least 8 characters long')
            .required('Password is required'),
    })
    


    const [ settings_on,setSettings_on ] = useState(false);
    const { REACT_APP_web_link_event } = process.env
    const [ contestants,setContestants ] = useState([]);

    useEffect(()=>{

      const interval = setInterval(() => {
        
        (async()=>{
          await axios.get(`${process.env.REACT_APP_web_contestants}?event=${event?.id}`)
          .then((response)=>{
            const sortedContestants = [...response.data].sort((a, b) => b.votes - a.votes);
            // setContestants(sortedContestants);

            setContestants(sortedContestants)
          })
          .catch((e)=>{
            console.log(e)
          })
        })()

      }, 1000);

      return ()=>clearInterval(interval)

    },[event?.id])

    const position = (pos) => {
      pos = parseInt(pos); // Ensure the input is an integer
      if (pos % 100 >= 11 && pos % 100 <= 13) {
        return pos + 'th';
      }
      switch (pos % 10) {
        case 1:
          return pos + 'st';
        case 2:
          return pos + 'nd';
        case 3:
          return pos + 'rd';
        default:
          return pos + 'th';
      }
    };

  return (
    <div className='realtive flex flex-col xs:px-[20px] md:px-[40px]  lg:px-[191px] w-full xs:mt-[59px] lg:mt-[20px]'>
        {/* yall fanatics { id } {JSON.stringify(event)} */}

        <h2 className='text-blue-700 font-bold border-t-4 border-red-500  border-b-4 w-auto max-w-[150px] xs:text-center md:text-start text-2xl xs:mb-[30px] md:mb-[40px] capitalize'>{event?.name_of_event}</h2>

        <div className='z-30 realtive flex flex-row items-center justify-center'>

        <svg xmlns="http://www.w3.org/2000/svg" onClick={()=>setSettings_on(!settings_on)}  fill="none" viewBox="0 0 24 24" strokeWidth={1.5} stroke="currentColor" className={`absolute right-[60px] top-[120px] size-9 ${!settings_on?'text-black':'text-red-500'} duration-200 ease-in-out cursor-pointer`}>
        <path strokeLinecap="round" strokeLinejoin="round" d="m14.74 9-.346 9m-4.788 0L9.26 9m9.968-3.21c.342.052.682.107 1.022.166m-1.022-.165L18.16 19.673a2.25 2.25 0 0 1-2.244 2.077H8.084a2.25 2.25 0 0 1-2.244-2.077L4.772 5.79m14.456 0a48.108 48.108 0 0 0-3.478-.397m-12 .562c.34-.059.68-.114 1.022-.165m0 0a48.11 48.11 0 0 1 3.478-.397m7.5 0v-.916c0-1.18-.91-2.164-2.09-2.201a51.964 51.964 0 0 0-3.32 0c-1.18.037-2.09 1.022-2.09 2.201v.916m7.5 0a48.667 48.667 0 0 0-7.5 0" />
        </svg>

        {
        settings_on &&<div onClick={()=>{
        setSettings_on(false)
        setDeleteScreen(false)
      }} className='overflow-y-clip cursor-pointer w-full h-screen absolute top-0 left-0 z-40 bg-black opacity-60'></div>}

        {settings_on&&
      <Formik
      validationSchema={PasswordValidationSchema}
      initialValues={{
        password:''
      }}
      onSubmit={(values)=>{

        setLoader(true)
        setTimeout(async() => {
          
          await axios.delete(`${REACT_APP_web_link_event}${id}/?password=${values.password}`,{
            headers:{
                Authorization:`Bearer ${userdata?.access}`
            }
          })
          .then((response)=>{  
            Swal.fire({
              icon:'success',
              title:`Event Deleted`,
              showConfirmButton:true,
            }).then((response)=>{
              window.location.href='/'
            })
            
          })
          .catch((e)=>{
            console.log(e)
            Swal.fire({
              icon:'error',
              title:`Failed to delete event`,
              text:`Double check your entered password or try again later`,
              // timer:5000,
              showConfirmButton:true
            })
          }).finally(()=>setLoader(false))
        }, 1000);
      }}
      >
        {((props)=>(

       <div className='z-50 absolute xs:top-64 xs:left-9 lg:top-1/3 lg:left-1/3 transition-all duration-300 ease-in-out xs:w-[80%] md:w-1/3 h-[340px] bg-white rounded-[12px] shadow-2xl py-4 px-[24px] flex flex-col items-center justify-between'>

      <h2 className='text-red-500 font-bold text-2xl'>Permenently Delete Event</h2>
      <h2 className='text-center text-gray-600 px-[10px]'>Once the event is deleted it cannot be recovered. Are you sure you want to delete it?</h2>
      

      <input 
      className='outline-none px-[10px] py-[8px] bg-gray-200 shadow-lg rounded-lg w-full my-3'
      type='password'
      placeholder='enter password here'
      value={props.values.password}
      onChange={props.handleChange('password')}
      onBlur={props.handleBlur('password')}
      />

       <p className='w-full text-center text-red-500 my-2'>{props.touched.password && props.errors.password}</p>
      <div onClick={props.handleSubmit} className='w-full flex flex-row items-center justify-between pl-[40px]'>

       
      {loader ?
       <BeatLoader
       color='red'
       size={22}
       />: <button className='-translate-x-[25%] cursor-pointer text-white px-4 py-2 rounded-[12px] bg-red-500 font-bold text-xl'>Yes I am</button>}

      <p onClick={()=>{
        setSettings_on(!settings_on)
        setDeleteScreen(!deletescreen)
        }} className='-translate-x-[25%] cursor-pointer text-white px-4 py-2 rounded-[12px] bg-gray-500 font-bold text-xl'>Cancel</p>

      </div>

      </div>
        ))}

      </Formik>
      }



      </div>

      <div className='w-full flex xs:flex-col-reverse md:flex-row items-center justify-between'>

      {event?.event_picture &&
      <div className='w-full relative'>

          <img
          
          src={`${process.env.REACT_APP_link}${event?.event_picture}`}
          alt={`${event?.name}`}
          className={`cursor-pointer transition-all duration-300 ease-in-out  bg-empty_event_card md:w-[550px] md:h-[480px] xs:w-[330px] xs:mt-6 md:mt-0 xs:h-[330px] rounded-[12px] shadow-xl flex flex-row items-center justify-center mr-[40px] mb-[50px]`}
          onClick={()=>window.open(`${process.env.REACT_APP_link}${event?.event_picture}`,'_blank')}
          />
          <svg onClick={()=>navigate('/update_event',{
            state:{
                event,
                user:userdata
            }
          })} xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" strokeWidth={1.5} stroke="currentColor" className="xs:size-7 md:size-12 cursor-pointer absolute xs:top-[30px] xs:left-[345px] md:top-[1px] md:left-[575px] lg:top-[3px] lg:left-[550px] text-black">
          <path strokeLinecap="round" strokeLinejoin="round" d="M9.594 3.94c.09-.542.56-.94 1.11-.94h2.593c.55 0 1.02.398 1.11.94l.213 1.281c.063.374.313.686.645.87.074.04.147.083.22.127.325.196.72.257 1.075.124l1.217-.456a1.125 1.125 0 0 1 1.37.49l1.296 2.247a1.125 1.125 0 0 1-.26 1.431l-1.003.827c-.293.241-.438.613-.43.992a7.723 7.723 0 0 1 0 .255c-.008.378.137.75.43.991l1.004.827c.424.35.534.955.26 1.43l-1.298 2.247a1.125 1.125 0 0 1-1.369.491l-1.217-.456c-.355-.133-.75-.072-1.076.124a6.47 6.47 0 0 1-.22.128c-.331.183-.581.495-.644.869l-.213 1.281c-.09.543-.56.94-1.11.94h-2.594c-.55 0-1.019-.398-1.11-.94l-.213-1.281c-.062-.374-.312-.686-.644-.87a6.52 6.52 0 0 1-.22-.127c-.325-.196-.72-.257-1.076-.124l-1.217.456a1.125 1.125 0 0 1-1.369-.49l-1.297-2.247a1.125 1.125 0 0 1 .26-1.431l1.004-.827c.292-.24.437-.613.43-.991a6.932 6.932 0 0 1 0-.255c.007-.38-.138-.751-.43-.992l-1.004-.827a1.125 1.125 0 0 1-.26-1.43l1.297-2.247a1.125 1.125 0 0 1 1.37-.491l1.216.456c.356.133.751.072 1.076-.124.072-.044.146-.086.22-.128.332-.183.582-.495.644-.869l.214-1.28Z" />
          <path strokeLinecap="round" strokeLinejoin="round" d="M15 12a3 3 0 1 1-6 0 3 3 0 0 1 6 0Z" />
        </svg>

          {/* <svg onClick={()=>navigate('/update_event',{
            state:{
                event,
                user:userdata
            }
          })} xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" strokeWidth={1.5} stroke="currentColor" className="size-6 cursor-pointer absolute bottom-[10px] left-[540px] text-red-500">
        <path strokeLinecap="round" strokeLinejoin="round" d="m16.862 4.487 1.687-1.688a1.875 1.875 0 1 1 2.652 2.652L6.832 19.82a4.5 4.5 0 0 1-1.897 1.13l-2.685.8.8-2.685a4.5 4.5 0 0 1 1.13-1.897L16.863 4.487Zm0 0L19.5 7.125" />
        </svg> */}

      </div>
        }

        <div className='flex xs:flex-row md:flex-col xs:-translate-x-14 md:-translate-x-3 lg:-translate-x-24'>

        <div className='flex flex-row items-center justify-center'>

        <svg xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" strokeWidth={1.5} stroke="currentColor" className="size-12 text-green-500">
        <path strokeLinecap="round" strokeLinejoin="round" d="M5.25 5.653c0-.856.917-1.398 1.667-.986l11.54 6.347a1.125 1.125 0 0 1 0 1.972l-11.54 6.347a1.125 1.125 0 0 1-1.667-.986V5.653Z" />
        </svg>
        {event?.from_date.toString().split('T')[0]}

        </div>

        <div className='flex flex-row items-center justify-center '>

        <svg xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" strokeWidth={1.5} stroke="currentColor" className="size-12 text-red-500">
        <path strokeLinecap="round" strokeLinejoin="round" d="M5.25 7.5A2.25 2.25 0 0 1 7.5 5.25h9a2.25 2.25 0 0 1 2.25 2.25v9a2.25 2.25 0 0 1-2.25 2.25h-9a2.25 2.25 0 0 1-2.25-2.25v-9Z" />
        </svg>

        {event?.to_date.toString().split('T')[0]}

        </div>

        



        </div>



      </div>

      <div className='w-full'>
      <button onClick={()=>navigate('/add_contestant',{
        state:{
          user:userdata,
          event:event
        }
      })} className='my-8 xs:mt-[40px] lg:mt-[34px] font-bold text-white bg-new_green w-[186px] h-[53px] rounded-[12px] shadow-md'>
          Add Contestant
      </button> 
      </div>



      <div className='xs:ml-[30px] md:ml-0 w-full flex flex-row items-start justify-start xs:h-[400px] md:h-[600px] my-[10px] overflow-y-auto'>

            <div className=' mt-[30px] w-full flex flex-wrap'>

            {
              contestants.length === 0 &&
              <div 
              className={`cursor-pointer transition-all duration-300 ease-in-out hover:-rotate-[35deg] bg-empty_event_card lg:w-[150px] lg:h-[150px] xs:w-[120px] xs:h-[120px] rounded-[12px] hover:shadow-xl flex flex-row items-center justify-center mr-[40px] mb-[50px]`}>
                  <h2 className='text-white font-bold text-lg text-center'>No Contestants Found</h2>
              </div>
            }
            {contestants.length === 0&&<ClipLoader
       color='green'
       size={32}
       className='mt-2 mb-6'
       />}

          {Array.isArray(contestants) && contestants.length > 0 && contestants?.map((item,index)=>{
            const primeImage = item?.contestant_images.find(image => image.prime_image) || item?.contestant_images[0];
            return(
              <div className='flex flex-row items-start justify-center shadow-lg rounded-[12px] mr-10 px-6 py-8 mb-4'>
             <img           
             src={`${process.env.REACT_APP_link}${primeImage?.contestantdata_images}`}
             alt={`${item?.name}`}
             className={`cursor-pointer transition-all duration-300 ease-in-out hover:-rotate-[35deg] bg-empty_event_card lg:w-[150px] lg:h-[150px] xs:w-[120px] xs:h-[120px] rounded-[12px] hover:shadow-xl flex flex-row items-center justify-center mr-[40px] mb-[50px]`}
             onClick={()=>window.open(`${process.env.REACT_APP_link}${primeImage?.contestantdata_images}`,'_blank')}
             />
   
             <div className='flex flex-col'>
   
           <button onClick={()=>navigate(`/single_contestant/${item.id}/`,{
              state:{
                user:userdata,
                event,
                contestant:item,
                primeImage,
                position:position(index+1)
              }
             })} className='xs:mt-[4px] font-bold text-white bg-blue-500 w-[80px] h-[53px] rounded-[12px] shadow-md mb-[20px]'>
             View
             </button> 
   
             <button onClick={()=>navigate('/vote-for-contestant',{
              state:{
                user:userdata,
                event,
                contestant:item,
                primeImage,
              }
             })} className='xs:mt-[4px] font-bold text-white bg-new_green w-[80px] h-[53px] rounded-[12px] shadow-md mb-[20px]'>
             Vote
             </button> 

             <div className='text-blue-500'><strong> {position(index+1)} </strong> </div>
             <div className='text-red-500'><strong>{item?.votes} </strong>Votes  </div>
             

   
             </div>
   
             </div>


          )
          }
          )} 

         

          </div>

      </div>
      

    </div>
  )
}
