import axios from 'axios'
import { Formik } from 'formik'
import React from 'react'
import { useLocation, useNavigate } from 'react-router-dom'
import Swal from 'sweetalert2'
import * as Yup from 'yup'

export default function Change_password() {

    const navigate = useNavigate()

    const location = useLocation()
    const { user } = location.state || {};

    const PasswordValidation =  Yup.object({
        password: Yup.string()
        .matches(/[A-Z]/, 'Password must contain at least one uppercase letter')
        .matches(/[a-z]/, 'Password must contain at least one lowercase letter')
        .matches(/\d/, 'Password must contain at least one number')
        .matches(/[@$!%*?&#]/, 'Password must contain at least one special character')
        .min(8, 'Password must be at least 8 characters long')
        .required('Password is required'),
    confirm_password: Yup.string()
        .oneOf([Yup.ref('password'), null], 'Passwords must match')
        .required('Confirm password is required'),
    })
    const REACT_APP_web_link = process.env.REACT_APP_web_link

  return (
    <div className='w-full xs:px-[22px] md:px-[66px] flex flex-col items-center justify-center pt-12'>

        <Formik
        validationSchema={PasswordValidation}
        initialValues={{
            password:'',
            confirm_password:''
        }}
        onSubmit={((values)=>{

            // localhost:8000/users/users/verify_email/?email
            setTimeout(async() => {
                await axios.put(`${REACT_APP_web_link}users/change_password/`,{...values,email:user?.email})
                .then((response)=>{
                    Swal.fire({
                        icon:'success',
                        timer:3000,
                        timerProgressBar:true,
                        showConfirmButton:false
                    }).then(()=>{
                        navigate('/sign-in-event-organizer')
                    })
                }).catch((e)=>{

                    console.log(e)
                    Swal.fire({
                        icon:'error',
                        title:'Unable to change password please try again',
                        timer:3000,
                        timerProgressBar:true,
                        showConfirmButton:false
                    })
                })
            }, 1000);

        })}
        >
            {((props)=>(
                <div className='xs:w-full md:w-[400px] bg-white shadow-lg rounded-[12px] flex flex-col items-center justify-center'>
                    <h2 className='text-center my-3'>Change Password</h2>
                    <p className='text-gray-400 text-md text-center'>Please enter your passwords below</p>
                    <input
                     type='password'
                     placeholder='password'
                      onChange={props.handleChange('password')} onBlur={props.handleBlur('password')}
                     className='px-[8px] py-[4px] outline-none mx-auto bg-input_field mt-[22px] xs:w-full lg:w-[319px] h-[63px] rounded-[12px] shadow-lg'
                     />
                     <p className=' text-center w-full mt-2 text-red-500 font-bold'>{props.touched.password && props.errors.password}</p>
                     <input
                     type='password'
                     placeholder='confirm password'
                      onChange={props.handleChange('confirm_password')} onBlur={props.handleBlur('confirm_password')}
                     className='px-[8px] py-[4px] outline-none mx-auto bg-input_field mt-[22px] xs:w-full lg:w-[319px] h-[63px] rounded-[12px] shadow-lg'
                     />
                     <p className=' text-center w-full mt-2 text-red-500 font-bold'>{props.touched.confirm_password && props.errors.confirm_password}</p>
                    
                    <button onClick={props.handleSubmit} className='xs:mt-[40px] lg:mt-[34px] mb-10 font-bold text-white bg-new_green w-[186px] h-[53px] rounded-[12px] shadow-md'>
                        Send
                    </button> 
                </div>
            ))}
        </Formik>
      
    </div>
  )
}
