import axios from 'axios';
import { Formik } from 'formik';
import React,{useEffect, useRef, useState}  from 'react'
import { useLocation, useNavigate } from 'react-router-dom';
import Swal from 'sweetalert2';
import * as Yup from 'yup';
export default function Verificationcode({event_signup_user}) {
    
const location = useLocation();
const { user } = location.state || {};
const navigate = useNavigate()
// navigate('/event_organizer_signup_pay', { state: { user: values } });

const VerificationValidation = Yup.object({
    first:Yup.string()
    .required(1,'Field is required')
    .matches(/^[0-9]$/, 'Must be a number'),
    second:Yup.string()
    .required(1,'Field is required')
    .matches(/^[0-9]$/, 'Must be a number'),
    third:Yup.string()
    .required(1,'Field is required')
    .matches(/^[0-9]$/, 'Must be a number'),
    fourth:Yup.string()
    .required(1,'Field is required')
    .matches(/^[0-9]$/, 'Must be a number'),
    fifth:Yup.string()
    .required(1,'Field is required')
    .matches(/^[0-9]$/, 'Must be a number'),
})

const REACT_APP_web_link = process.env.REACT_APP_web_link

const hasRun = useRef(false);

  useEffect(() => {
    if (hasRun.current) return; // Prevents the effect from running again

    setTimeout(() => {
      const sendCode = async () => {
        try {
          const response = await axios.get(
            `${process.env.REACT_APP_web_link}send_verification_code/${user?.email}/`
          );
          setVerification_code(response.data);
        } catch (e) {
          console.log(e);
        }
      };
      sendCode();
    }, 200);

    hasRun.current = true;
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

const [verification_code,setVerification_code] = useState([])


  return (
    <div className='w-full h-screen xs:px-[4px] md:px-[12px] pt-[12px]'>
      
      <h2 className='text-center font-bold text-2xl '> Enter verification code</h2>

      <p className='xs:my-12 md:my-4 text-center text-gray-500'>Enter code that we have sent to your email your {user?.email.replace(/^[^@]+/, "...")}</p>

      <Formik
      validationSchema={VerificationValidation}
      onSubmit={((values)=>{

        if(parseInt(verification_code[0]) ===parseInt(values.first) && parseInt(verification_code[1]) ===parseInt(values.second) && parseInt(verification_code[2]) ===parseInt(values.third) && parseInt(verification_code[3]) ===parseInt(values.fourth) && parseInt(verification_code[4]) ===parseInt(values.fifth) ) { 
          
          // user?.forgotten_password?'':navigate('/event_organizer_signup_pay', { state: { user } })
          if( Object.keys(user).includes('forgotten_password') ){
            navigate('/enter_new_password', { state: { user } })
          }else{
            navigate('/event_organizer_signup_pay', { state: { user } })
          }

        }

        else{
            Swal.fire({
                icon:'error',
                title:'Incorrect code',
                timer:3000,
                timerProgressBar:true,
                showConfirmButton:false
            })
        }
        
      })}
      initialValues={{
        first:'',
        second:'',
        third:'',
        fourth:'',
        fifth:'',
      }}
      >
        {((props)=>(
            <div className='w-full flex flex-col items-center justify-center'>
               
            <div className='w-full flex flex-row justify-between items-center mt-[20px] xs:px-[12px] md:px-0 xs:w-full md:w-[400px] lg:w-[400px] mx-auto'>

                <input
                className={`w-[60px] h-[60px] rounded-[12px] shadow-xl ${props.touched.first && props.errors.first ? 'border-2 border-red-500':' border border-black'} text-center outline-none text-2xl`}
                maxLength={1}
                onChange={props.handleChange('first')}
                onBlur={props.handleBlur('first')}
                />
                <input
                className={`w-[60px] h-[60px] rounded-[12px] shadow-xl ${props.touched.second && props.errors.second ? 'border-2 border-red-500':' border border-black'} text-center outline-none text-2xl`}
                maxLength={1}
                onChange={props.handleChange('second')}
                onBlur={props.handleBlur('second')}
                />
                <input
                className={`w-[60px] h-[60px] rounded-[12px] shadow-xl ${props.touched.third && props.errors.third ? 'border-2 border-red-500':' border border-black'} text-center outline-none text-2xl`}
                maxLength={1}
                onChange={props.handleChange('third')}
                onBlur={props.handleBlur('third')}
                />
                <input
                className={`w-[60px] h-[60px] rounded-[12px] shadow-xl ${props.touched.fourth && props.errors.fourth ? 'border-2 border-red-500':' border border-black'} text-center outline-none text-2xl`}
                maxLength={1}
                onChange={props.handleChange('fourth')}
                onBlur={props.handleBlur('fourth')}
                />
                <input
                className={`w-[60px] h-[60px] rounded-[12px] shadow-xl ${props.touched.fifth && props.errors.fifth ? 'border-2 border-red-500':' border border-black'} text-center outline-none text-2xl`}
                maxLength={1}
                onChange={props.handleChange('fifth')}
                onBlur={props.handleBlur('fifth')}
                />

                


            </div>
                <button onClick={props.handleSubmit} className='xs:mt-[40px] lg:mt-[34px] font-bold text-white bg-new_green w-[186px] h-[53px] rounded-[12px] shadow-md'>
                Confirm
                </button> 
            </div>
        ))}
        
      </Formik>

    </div>
  )
}
