import React, { useEffect, useState } from 'react'
import Cookies from 'js-cookie'
import { motion } from 'framer-motion'
import { Formik, isEmptyArray } from 'formik'
import * as Yup from 'yup';
import Swal from 'sweetalert2';
import axios from 'axios';
import { BeatLoader } from 'react-spinners';
import { useNavigate } from 'react-router-dom';
export default function Event_home({loggedinuser}) {

  const navigate = useNavigate()
  const [ userdata,setUserdata ] = useState('')
  const [ eventData,seteventData ] = useState([])
  const [ loader,setLoader ] = useState(false)
  const [ settings_on,setSettings_on ] = useState(false)
  const [ deletescreen,setDeleteScreen ] = useState(false)
  const [ eventaction,setEventaction ] = useState(false)

  const REACT_APP_web_link = process.env.REACT_APP_web_link

  const REACT_APP_web_link_event = process.env.REACT_APP_web_link_event

  useEffect(()=>{
    
    const interval = setInterval(() => {
      const fetchuserEvents=async()=>{
        await axios.get(`${REACT_APP_web_link_event}?user_id=${userdata?.id}`,{
          headers:{
            Authorization:`Bearer ${userdata?.access}`
          }
        })
        .then((response)=>{
          seteventData(response.data)
        }).catch((e)=>{
          console.log(e)
        })
      }
      fetchuserEvents()
    }, 1000);

    return ()=> clearInterval(interval)
    
  },[REACT_APP_web_link_event, userdata?.access, userdata?.id])
  
    const handleRightClick = (event,id) => {
      event.preventDefault(); // Prevent the default context menu from appearing
      console.log('Right-click detected',id);
      // Add your custom logic here
    };

  const PasswordValidationSchema = Yup.object({
    password: Yup.string()
        .matches(/[A-Z]/, 'Password must contain at least one uppercase letter')
        .matches(/[a-z]/, 'Password must contain at least one lowercase letter')
        .matches(/\d/, 'Password must contain at least one number')
        .matches(/[@$!%*?&#]/, 'Password must contain at least one special character')
        .min(8, 'Password must be at least 8 characters long')
        .required('Password is required'),
})


  useEffect(()=>{
    const user = Cookies.get('user')
    if(user){
    setUserdata(JSON.parse(user))
    }
  },[])

  const InitialVariants = {
    initial:{
      'x':'110vw'
    },
    animate:{
      'x':'0vw',
      transition:{
        duration:0.05,
        ease:'easeInOut'
      }
    }
  }

  const cardvariants={
    initial:{
        'opacity':0.6,
    },
    animate:{
        'opacity':1,
        transition:{
            ease:'easeInOut',
            duration:2
        }
    }
}

const variants={
    initial:{
        'opacity':0,
    },
    animate:{
        'opacity':1,
        transition:{
            ease:'easeInOut',
            duration:2
        }
    }
}
  return (
    <div className='z-30 w-full'>

      <div className='z-30 w-full flex flex-row items-center justify-end my-4 xs:px-[28px] lg:px-[58px]'>

      <div className='z-30 realtive flex flex-row items-center justify-center'>

      <svg onClick={()=>setSettings_on(!settings_on)} xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" strokeWidth={1.5} stroke="currentColor" className={`size-9 ${settings_on?'text-blue-500':''} duration-200 ease-in-out cursor-pointer`}>
        <path strokeLinecap="round" strokeLinejoin="round" d="M10.343 3.94c.09-.542.56-.94 1.11-.94h1.093c.55 0 1.02.398 1.11.94l.149.894c.07.424.384.764.78.93.398.164.855.142 1.205-.108l.737-.527a1.125 1.125 0 0 1 1.45.12l.773.774c.39.389.44 1.002.12 1.45l-.527.737c-.25.35-.272.806-.107 1.204.165.397.505.71.93.78l.893.15c.543.09.94.559.94 1.109v1.094c0 .55-.397 1.02-.94 1.11l-.894.149c-.424.07-.764.383-.929.78-.165.398-.143.854.107 1.204l.527.738c.32.447.269 1.06-.12 1.45l-.774.773a1.125 1.125 0 0 1-1.449.12l-.738-.527c-.35-.25-.806-.272-1.203-.107-.398.165-.71.505-.781.929l-.149.894c-.09.542-.56.94-1.11.94h-1.094c-.55 0-1.019-.398-1.11-.94l-.148-.894c-.071-.424-.384-.764-.781-.93-.398-.164-.854-.142-1.204.108l-.738.527c-.447.32-1.06.269-1.45-.12l-.773-.774a1.125 1.125 0 0 1-.12-1.45l.527-.737c.25-.35.272-.806.108-1.204-.165-.397-.506-.71-.93-.78l-.894-.15c-.542-.09-.94-.56-.94-1.109v-1.094c0-.55.398-1.02.94-1.11l.894-.149c.424-.07.765-.383.93-.78.165-.398.143-.854-.108-1.204l-.526-.738a1.125 1.125 0 0 1 .12-1.45l.773-.773a1.125 1.125 0 0 1 1.45-.12l.737.527c.35.25.807.272 1.204.107.397-.165.71-.505.78-.929l.15-.894Z" />
        <path strokeLinecap="round" strokeLinejoin="round" d="M15 12a3 3 0 1 1-6 0 3 3 0 0 1 6 0Z" />
      </svg>

      </div>

      {
        eventaction && 
        <div className='z-50 absolute xs:top-64 xs:left-9 lg:top-1/3 lg:left-1/3 transition-all duration-300 ease-in-out xs:w-[80%] md:w-1/3 h-screen bg-white rounded-[12px] shadow-2xl py-4 px-[24px] flex flex-col items-center justify-between'>
        </div>
      }

        {
        eventaction &&<div onClick={()=>{
        setEventaction(false)
        setSettings_on(false)
        setDeleteScreen(false)
      }} className='overflow-y-clip cursor-pointer w-full h-screen absolute top-0 left-0 z-40 bg-black opacity-60'></div>}

      {(settings_on || deletescreen) &&<div onClick={()=>{
        setSettings_on(false)
        setDeleteScreen(false)
      }} className='cursor-pointer w-full h-screen absolute top-0 left-0 z-40 bg-black opacity-60'></div>}

      {deletescreen&&
      <Formik
      validationSchema={PasswordValidationSchema}
      initialValues={{
        password:''
      }}
      onSubmit={(values)=>{

        setLoader(true)
        setTimeout(async() => {
          
          await axios.delete(`${REACT_APP_web_link}${userdata?.email}/?password=${values.password}`)
          .then((response)=>{  
            Swal.fire({
              icon:'success',
              title:`User Deleted`,
              showConfirmButton:true,
            }).then((response)=>{
              window.location.href='/'
            })
            const user_cookie = Cookies.get('user')
            if(user_cookie) return Cookies.remove('user')   
            
          })
          .catch((e)=>{
            console.log(e)
            Swal.fire({
              icon:'error',
              title:`Failed to delete account`,
              text:`Double check your entered password or try again later`,
              // timer:5000,
              showConfirmButton:true
            })
          }).finally(()=>setLoader(false))
        }, 1000);
      }}
      >
        {((props)=>(

       <div className='z-50 absolute xs:top-64 xs:left-9 lg:top-1/3 lg:left-1/3 transition-all duration-300 ease-in-out xs:w-[80%] md:w-1/3 h-[340px] bg-white rounded-[12px] shadow-2xl py-4 px-[24px] flex flex-col items-center justify-between'>

      <h2 className='text-red-500 font-bold text-2xl'>Permenently Delete Account</h2>
      <h2 className='text-center text-gray-600 px-[10px]'>Once the account is deleted it cannot be recovered. Are you sure you want to delete your account?</h2>
      

      <input 
      className='outline-none px-[10px] py-[8px] bg-gray-200 shadow-lg rounded-lg w-full my-3'
      type='password'
      placeholder='enter password here'
      value={props.values.password}
      onChange={props.handleChange('password')}
      onBlur={props.handleBlur('password')}
      />

       <p className='w-full text-center text-red-500 my-2'>{props.touched.password && props.errors.password}</p>
      <div onClick={props.handleSubmit} className='w-full flex flex-row items-center justify-between pl-[40px]'>

       
      {loader ?
       <BeatLoader
       color='red'
       size={22}
       />: <button className='-translate-x-[25%] cursor-pointer text-white px-4 py-2 rounded-[12px] bg-red-500 font-bold text-xl'>Yes I am</button>}

      <p onClick={()=>{
        setSettings_on(!settings_on)
        setDeleteScreen(!deletescreen)
        }} className='-translate-x-[25%] cursor-pointer text-white px-4 py-2 rounded-[12px] bg-gray-500 font-bold text-xl'>Cancel</p>

      </div>

      </div>
        ))}

      </Formik>
      }

      {
        settings_on&&
      <motion.div
      variants={InitialVariants}
      initial='initial'
      animate='animate'
      transition='transition'

      className='z-50 absolute top-32 right-[100px] transition-all duration-300 ease-in-out w-[300px] h-[170px] bg-white rounded-[12px] shadow-xl py-4 px-[24px] flex flex-col items-center justify-between'>

      <h2 className='w-full text-start text-blue-500  font-bold text-2xl'>{userdata?.name}</h2>
      <h2 className='w-full text-start text-blue-500 font-bold text-lg'>{userdata?.organization}</h2>
      {/* <h2 className='w-full text-new_green font-bold text-lg '>{userdata?.email}</h2>
      <h2 className='w-full text-new_green font-bold text-lg '>{userdata?.phone}</h2> */}

      <button onClick={()=>{
        setSettings_on(!settings_on)
        setDeleteScreen(true)
      }} className='-translate-x-[25%] cursor-pointer text-white px-4 py-2 rounded-[12px] bg-red-500 font-bold text-xl'>Delete Account</button>

      </motion.div>
      }

      </div>

      <motion.div
    variants={variants}
    transition='transition'
    initial='initial'
    whileInView="animate"
    viewport={{once:true}}
     className='xs:mt-[23px] lg:mt-[100px] w-full lg:px-[188px] xs:px-[20px] md:px-[40px] flex flex-col items-center justify-center'>

      <button onClick={()=>navigate('/create_event',{
        state:{
          user:userdata
        }
      })} className='md:-translate-x-[270px] lg:-translate-x-[500px] my-8 xs:mt-[40px] lg:mt-[34px] font-bold text-white bg-new_green w-[186px] h-[53px] rounded-[12px] shadow-md'>
          Add Event
      </button> 

        <h2 className='text-blue-700 font-bold border-t-4 border-red-500  border-b-4 w-[100px] xs:text-center text-2xl xs:mb-[30px] md:mb-[40px] '>EVENTS</h2>


            <div className='xs:ml-[30px] md:ml-0 w-full flex flex-row items-start justify-start xs:h-[400px] md:h-[600px] my-[10px] overflow-y-auto'>

                <div className=' mt-[30px] w-full flex flex-wrap'>
                  
                  {
                    Array.isArray(eventData) && isEmptyArray(eventData)&& <motion.div 
                    variants={cardvariants}
                    transition='transition'
                    initial='initial'
                    whileInView="animate"
                    viewport={{once:true}}
                    className={`cursor-pointer transition-all duration-300 ease-in-out hover:-rotate-[35deg] bg-empty_event_card lg:w-[150px] lg:h-[150px] xs:w-[120px] xs:h-[120px] rounded-[12px] hover:shadow-xl flex flex-row items-center justify-center mr-[40px] mb-[50px]`}>
                        <h2 className='text-white font-bold text-xl text-center'>No Events Found</h2>
                    </motion.div>
                  }
                    {
                      // Array(1).fill(1)
                    Array.isArray(eventData) && eventData?.map((item,index)=>(
                  <div 
                  onClick={()=>navigate(`/single_event/${item?.id}`,{
                    state:{
                      event:item
                    }
                  })}
                  key={index}>
                  {
                  item?.event_picture ?
                  <motion.img
                  
                  src={`${process.env.REACT_APP_link}${item?.event_picture}`}
                  alt={`${item?.name}`}
                  className={`cursor-pointer transition-all duration-300 ease-in-out hover:-rotate-[35deg] bg-empty_event_card lg:w-[150px] lg:h-[150px] xs:w-[120px] xs:h-[120px] rounded-[12px] hover:shadow-xl flex flex-row items-center justify-center mr-[40px] mb-[50px]`}
                  // onClick={() => window.open(`${process.env.REACT_APP_link}${item?.event_picture}`, '_blank')}
                  // onContextMenu={(e)=>handleRightClick(e,item?.id)}
                  // onDoubleClick={() => setEventaction(true) }

                  />
                  :
                <motion.div 
                variants={cardvariants}
                transition='transition'
                initial='initial'
                whileInView="animate"
                viewport={{once:true}}
                className={`cursor-pointer transition-all duration-300 ease-in-out hover:-rotate-[35deg] bg-empty_event_card lg:w-[150px] lg:h-[150px] xs:w-[120px] xs:h-[120px] rounded-[12px] hover:shadow-xl flex flex-row items-center justify-center mr-[40px] mb-[50px]`}>
                    <h2 className='text-white font-bold text-xl text-center'>Picture not available</h2>
                </motion.div>
                  }
                  </div>
                        ))
                    }

                
                </div>
            </div>

      </motion.div>

    </div>
  )
}
