/* eslint-disable react/jsx-pascal-case */
import { BrowserRouter, Route, Routes } from "react-router-dom";
import HomePage from "./components";
import NavBar from "./components/nav";
import { useEffect, useState } from "react";
import Event_organizer_signup from "./components/event_organizer_signup";
import Event_organizer_signin from "./components/event_organizer_signin";
import Event_organizer_signup_paystack from "./components/event_organizer_signup_paystack";
import Event_org_private from "./components/event-org_private";
import Cookies from "js-cookie";
import Event_home from "./components/Events/event_home";
import Verificationcode from "./components/verification-code";
import ForgottenPassword from "./components/Forgotten-Password";
import Change_password from "./components/change_password";
import CreateEvent from "./components/Events/create_event";
import Singleevent from "./components/Events/single_event";
import UpdateEvent from './components/Events/update_event';
import Createcontestant from "./components/contestants/create-contestant";
import ShowUpdateContestant from "./components/contestants/show-update-contestant";
import VoteContestant from "./components/contestants/vote-for-contestant";
import Vote_paystack from "./components/contestants/vote-paystack";
import SingleeventPublic from "./components/Events/event_public/single_event";
import VoteContestantPublic from "./components/Events/event_public/vote-for-contestant-public";
import Vote_paystack_pubic from "./components/Events/event_public/vote-payment-public";
function App() {
  
  const [loggedinuser,setLoggedinuser]=useState(null)

  const [event_signup_user,setEvent_signup_user]=useState(false)
  useEffect(()=>{
    const user = Cookies.get('user')

    const interval = setInterval(() => {
        if(user){
            const user_json = JSON.parse(user)
            setEvent_signup_user(true)
        }    
    }, 1000);

    return ()=>clearInterval(interval)

},[event_signup_user, setEvent_signup_user])

  useEffect(()=>{
    if(document.readyState){
      document.title='Voteify'
    }
  },[])
  return (
    <>
    <BrowserRouter>
    <div>
      
    </div>
    <NavBar 
    event_signup_user={event_signup_user}
    />
    <Routes>

      <Route
      element={<HomePage event_signup_user={event_signup_user}/>}
      path="/"
      exact
      
      />

      <Route
      element={<Event_organizer_signup setEvent_signup_user={setEvent_signup_user}/>}
      path="/become-an-event-supervisor"
      exact
      />

      
    <Route
      element={<Event_organizer_signin
        loggedinuser={loggedinuser}
        setLoggedinuser={setLoggedinuser}
        setEvent_signup_user={setEvent_signup_user}
        />}
      path="/sign-in-event-organizer"
      exact
      />


      <Route
      element={<ForgottenPassword setEvent_signup_user={setEvent_signup_user}/>}
      path="/forgotten_password"
      exact
      />


      <Route
      element={<SingleeventPublic/>}
      path="/single_event_public/:id"
      exact
      />

      <Route
      element={<VoteContestantPublic/>}
      path="/vote-for-contestant-public"
      exact
      />

      <Route
      element={<Vote_paystack_pubic/>}
      path="/voters-payment"
      exact
      />



    <Route element={<Event_org_private user={event_signup_user}/>}>
    
    <Route
      element={<Event_organizer_signup_paystack/>}
      path="/event_organizer_signup_pay"
      exact
      />

      <Route
      element={<Change_password/>}
      path="/enter_new_password"
      exact
      />



    <Route
      element={<Verificationcode
        event_signup_user={event_signup_user}
        />}
      path="/verification_code"
      exact
      />

      <Route
      element={<Event_home
      loggedinuser={loggedinuser}
      
      />}
      path="/event_home"
      exact
      />

      <Route
      element={<Singleevent
        loggedinuser={loggedinuser}
      />}
      path="/single_event/:id"
      exact
      />

      <Route
      element={<ShowUpdateContestant
        loggedinuser={loggedinuser}
      />}
      path="/single_contestant/:id"
      exact
      />

      <Route
      element={<Createcontestant
      />}
      path="/add_contestant/"
      exact
      />



      <Route
      element={<CreateEvent/>}
      path="/create_event"
      exact
      />

      <Route
      element={<VoteContestant/>}
      path="/vote-for-contestant"
      exact
      />

      <Route
      element={<Vote_paystack/>}
      path="/vote-payment"
      exact
      />

      <Route
      element={<UpdateEvent/>}
      path="/update_event"
      exact
      />


    </Route>



    <Route
      element={<HomePage/>}
      path="*"
      exact
      />

    </Routes>

    </BrowserRouter>
    </>
  );
}

export default App;
