import { Formik } from 'formik'
import React, { useState } from 'react'
import { Link } from 'react-router-dom'
import { BeatLoader } from 'react-spinners'
import * as Yup from 'yup'

import { useNavigate } from 'react-router-dom';

export default function Event_organizer_signup({setEvent_signup_user}) {
    const [loading,setLoading]=useState('')
    const navigate = useNavigate();

    

    // const config = {
    //     reference: (new Date()).getTime().toString(),
    //     email:'',
    //     currency:'GHS',
    //     amount: 1000, //Amount is in the country's lowest currency. E.g Kobo, so 20000 kobo = N200
    //     publicKey: REACT_APP_paystack_public_key,
    //     paymentOptions: 'card,mobilemoney,ussd',
    // };
    
    // // you can call this function anything
    // const onSuccess = (reference) => {
    //   // Implementation for whatever you want to do with reference and after success call.
    //   console.log('haha there',reference);
    //   Swal.fire({
    //     icon: 'success',
    //     title: 'Payment Successful',
    //     text: `Reference: ${JSON.stringify(reference)}`,
    //     timer: 5000,
    //     timerProgressBar: true,
    //     showConfirmButton: true,
    // });
    //   console.log('success\n son')
    // };
  
    // // you can call this function anything
    // const onClose = () => {
    //   // implementation for  whatever you want to do when the Paystack dialog closed.
    //   console.log('closed')
    // }

    // const initializePayment = usePaystackPayment(config);

    const EventOrganizationValidationSchema = Yup.object({
        name:Yup.string()
        .required('Name is required'),
        email:Yup.string()
        .email('Invalid email address')
        .required('Email is required'),
        password: Yup.string()
            .matches(/[A-Z]/, 'Password must contain at least one uppercase letter')
            .matches(/[a-z]/, 'Password must contain at least one lowercase letter')
            .matches(/\d/, 'Password must contain at least one number')
            .matches(/[@$!%*?&#]/, 'Password must contain at least one special character')
            .min(8, 'Password must be at least 8 characters long')
            .required('Password is required'),
        confirm_password: Yup.string()
            .oneOf([Yup.ref('password'), null], 'Passwords must match')
            .required('Confirm password is required'),
        phone:Yup.number('Phone number must be a number')
        .required('Phone number is required'),
        organization:Yup.string()
        .required('Please enter organization')
        .min(2,'Name of organization is too short')
    })
  return (
    <div className='xs:py-[32px] lg:py-[32px] w-full xs:px-[40px] lg:px-[191px] flex xs:flex-col lg:flex-row items-center justify-center h-full'>

        <img

        src={require('../assert/event_org_signup.png')}
        alt='event_org_signup'
        width={'539px'}
        height={'545px'}
        className='cursor-pointer'
        />

        <Formik
        validationSchema={EventOrganizationValidationSchema}
        initialValues={{
            name:'',
            email:'',
            password:'',
            confirm_password:'',
            phone:'',
            organization:'',
        }}
        onSubmit={((values)=>{
        setLoading(true)
        setEvent_signup_user(true)
        localStorage.setItem('user_data_to_sent',JSON.stringify(values))
        navigate('/verification_code', { state: { user: values } });
       
        })}
        >
            {((props)=>(
                <div className='flex flex-col items-start justify-start xs:my-[40px] lg:my-0 xs:ml-[20px] xs:mr-[20px] lg:mr-0 lg:ml-[138px] xs:px-[20px] lg:px-[0px] xs:h-[auto] lg:h-auto xs:w-full lg:w-[418px] h-full shadow-lg rounded-[12px]'>

                    <h2 className='text-center w-full font-bold text-2xl my-4'>Sign up as an organizer</h2>

                    <input
                    placeholder='name'
                     onChange={props.handleChange('name')} onBlur={props.handleBlur('name')}
                     className='px-[8px] py-[4px] outline-none mx-auto bg-input_field mt-[42px] xs:w-full lg:w-[319px] h-[63px] rounded-[12px] shadow-lg'
                     />
                     <p className=' text-center w-full mt-2 text-red-500 font-bold'>{props.touched.name && props.errors.name}</p>
                     <input
                    placeholder='email'
                     onChange={props.handleChange('email')} onBlur={props.handleBlur('email')}
                     className='px-[8px] py-[4px] outline-none mx-auto bg-input_field mt-[42px] xs:w-full lg:w-[319px] h-[63px] rounded-[12px] shadow-lg'
                     />
                     <p className=' text-center w-full mt-2 text-red-500 font-bold'>{props.touched.email && props.errors.email}</p>
                    
                     <input
                     type='password'
                     placeholder='password'
                      onChange={props.handleChange('password')} onBlur={props.handleBlur('password')}
                     className='px-[8px] py-[4px] outline-none mx-auto bg-input_field mt-[22px] xs:w-full lg:w-[319px] h-[63px] rounded-[12px] shadow-lg'
                     />
                     <p className=' text-center w-full mt-2 text-red-500 font-bold'>{props.touched.password && props.errors.password}</p>
                     <input
                     type='password'
                     placeholder='confirm password'
                      onChange={props.handleChange('confirm_password')} onBlur={props.handleBlur('confirm_password')}
                     className='px-[8px] py-[4px] outline-none mx-auto bg-input_field mt-[22px] xs:w-full lg:w-[319px] h-[63px] rounded-[12px] shadow-lg'
                     />
                     <p className=' text-center w-full mt-2 text-red-500 font-bold'>{props.touched.confirm_password && props.errors.confirm_password}</p>
                     <input
                     placeholder='phone'
                      onChange={props.handleChange('phone')} onBlur={props.handleBlur('phone')}
                     className='px-[8px] py-[4px] outline-none mx-auto bg-input_field mt-[22px] xs:w-full lg:w-[319px] h-[63px] rounded-[12px] shadow-lg'
                     />
                     <p className=' text-center w-full mt-2 text-red-500 font-bold'>{props.touched.phone && props.errors.phone}</p>
                     <input
                     placeholder='organization name'
                      onChange={props.handleChange('organization')} onBlur={props.handleBlur('organization')}
                     className='px-[8px] py-[4px] outline-none mx-auto bg-input_field mt-[22px] xs:w-full lg:w-[319px] h-[63px] rounded-[12px] shadow-lg'
                     />
                     <p className=' text-center w-full mt-2 text-red-500 font-bold'>{props.touched.organization && props.errors.organization}</p>

                    <div className='w-full flex flex-row items-center justify-center'>
                        {
                        loading?
                        <BeatLoader
                        color='#3EBF53'
                        size={32} 
                        />
                        :
                        <button onClick={props.handleSubmit} className='xs:mt-[40px] lg:mt-[34px] font-bold text-white bg-new_green w-[186px] h-[53px] rounded-[12px] shadow-md'>
                        Signup
                        </button> 
                        }

                        </div>
                        <Link to={'/sign-in-event-organizer'} className='mx-auto my-4'>
                        <p className='font-bold text-center text-xl'>Already an organizer? Sign in</p>
                        </Link>
                </div>
            ))}
        </Formik>
    </div>
  )
}
