import { Formik } from 'formik'
import React, { useState } from 'react'
import { Link, useLocation } from 'react-router-dom'
import { BeatLoader } from 'react-spinners'
import * as Yup from 'yup'

import { useNavigate } from 'react-router-dom';
import Resizer from "react-image-file-resizer";
import axios from 'axios'
import Cookies from 'js-cookie'
import Swal from 'sweetalert2'
export default function VoteContestantPublic() {
  const [loading,setLoading]=useState('')
  const [imagePreviewUrl, setImagePreviewUrl] = useState(null)
    const [event_organizer,setEvent_organizer]=useState(null)
    const [paid,setPaid]=useState(false)
    // const [user,setUser]=useState(null)

    const base64ToFile = (base64String, filename) => {
      let arr = base64String.split(',');
      let mime = arr[0].match(/:(.*?);/)[1];
      let bstr = atob(arr[1]);
      let n = bstr.length;
      let u8arr = new Uint8Array(n);
    
      while (n--) {
        u8arr[n] = bstr.charCodeAt(n);
      }
    
      return new File([u8arr], filename, { type: mime });
    };


    const location = useLocation();

    const { user,event,contestant,primeImage } = location.state || {}

    const navigate = useNavigate();

    const VoteForContestantSchema = Yup.object({
    name:Yup.string()
    .required('Name is required'),
    email:Yup.string()
    .email('Invalid email address')
    .required('Email is required'),
    phone:Yup.number('Phone number must be a number')
        .required('Phone number is required'),
    number_of_votes:Yup.number()
    .required('Please enter the number of votes'),
    });
  return (
    <div className='xs:py-[32px] lg:py-[32px] w-full xs:px-[40px] lg:px-[191px] flex xs:flex-col lg:flex-row items-center justify-center h-full'>

        <img

        src={`${process.env.REACT_APP_link}${primeImage?.contestantdata_images}`}
        alt='createevent.png'
        width={'539px'}
        height={'545px'}
        className='cursor-pointer'
        />

        <Formik
        validationSchema={VoteForContestantSchema}
        initialValues={{
            number_of_votes:'2',
        }}
        onSubmit={((values)=>{

          if(event?.paid){
            navigate('/voters-payment',{
              state:{
                user:{name:values.name,email:values.email,phone:values.phone},
                data:{
                event:event?.id,
                contestant:contestant?.id,
                vote:values.number_of_votes,
                amount_to_pay:parseFloat(values.number_of_votes)*parseFloat(event.amount),
                }
              }
            })
          }else{
            setLoading(!loading)
            setTimeout(async() => {
              await axios.post(`${process.env.REACT_APP_web_votes}`,{
                event:event?.id,
                contestant:contestant?.id,
                vote:values.number_of_votes
              })
              .then((response)=>{
                Swal.fire({
                  title:'Vote recorded',
                  icon:'success',
                  timer:3000,
                  timerProgressBar:false
            
                }).then(()=>{navigate('/event_home')})
            setLoading(!loading)

                
                }).catch((e)=>{
                  console.log(e)
                  Swal.fire({
                    title:'An error occurred',
                    icon:'error',
                  })
            setLoading(!loading)


              }).finally(()=>{
                setLoading(!loading)
                })
            }, 1000);
          }
            // console.log(values?.event_picture)

        // alert(JSON.stringify(values))
       
        })}
        >
            {((props)=>(
                <div className='flex flex-col items-start justify-start xs:my-[40px] lg:my-0 xs:ml-[20px] xs:mr-[20px] lg:mr-0 lg:ml-[138px] xs:px-[20px] lg:px-[0px] xs:h-[auto] lg:h-auto xs:w-full lg:w-[418px] h-full shadow-lg rounded-[12px]'>

                    <h2 className='text-center w-full font-bold text-2xl my-4 capitalize text-blue-500'>Vote for {contestant?.name}</h2>

                    <input
                    placeholder='name'
                     onChange={props.handleChange('name')} onBlur={props.handleBlur('name')}
                     className='px-[8px] py-[4px] outline-none mx-auto bg-input_field mt-[42px] xs:w-full lg:w-[319px] h-[63px] rounded-[12px] shadow-lg'
                     />
                     <p className=' text-center w-full mt-2 text-red-500 font-bold'>{props.touched.name && props.errors.name}</p>
                     <input
                    placeholder='email'
                     onChange={props.handleChange('email')} onBlur={props.handleBlur('email')}
                     className='px-[8px] py-[4px] outline-none mx-auto bg-input_field mt-[42px] xs:w-full lg:w-[319px] h-[63px] rounded-[12px] shadow-lg'
                     />
                     <p className=' text-center w-full mt-2 text-red-500 font-bold'>{props.touched.email && props.errors.email}</p>
                     <input
                    placeholder='phone'
                     onChange={props.handleChange('phone')} onBlur={props.handleBlur('phone')}
                     className='px-[8px] py-[4px] outline-none mx-auto bg-input_field mt-[42px] xs:w-full lg:w-[319px] h-[63px] rounded-[12px] shadow-lg'
                     />
                     <p className=' text-center w-full mt-2 text-red-500 font-bold'>{props.touched.phone && props.errors.phone}</p>
                    

                    
                     <select
                     className='px-[8px] py-[4px] outline-none mx-auto bg-input_field mt-[42px] xs:w-full lg:w-[219px] h-[63px] rounded-[12px] shadow-lg'
                     value={props.values.number_of_votes}
                     onChange={props.handleChange('number_of_votes')}
                     onBlur={props.handleBlur('number_of_votes')}
                     >
                        {Array(9).fill(10)?.map((item,index)=>(

                        <option key={index}>{index+2}</option>
                        ))}
                     </select>

                     <label className='py-[4px]  mt-[42px] xs:w-full lg:w-[219px] rounded-[12px] text-center'>Amount</label>
                     {event.paid===true ?<input 
                     className='px-[8px] py-[4px] border-4 border-blue-500 mx-auto bg-input_field xs:w-full lg:w-[219px] h-[63px] rounded-[12px] shadow-lg'
                     readOnly={true}
                     value={parseFloat(props.values.number_of_votes)* parseFloat(event.amount)}
                     />:
                     <input 
                     className='px-[8px] py-[4px] border-4 border-blue-500 mx-auto bg-input_field xs:w-full lg:w-[219px] h-[63px] rounded-[12px] shadow-lg'
                     readOnly={true}
                     value={0}
                     />}

                    <div className='w-full flex flex-row items-center justify-center'>
                        {
                        loading?
                        <BeatLoader
                        color='#3EBF53'
                        size={32} 
                        />
                        :
                        <button onClick={props.handleSubmit} className='xs:mt-[40px] lg:mt-[34px] font-bold text-white bg-new_green w-[186px] h-[53px] rounded-[12px] shadow-md mb-[20px]'>
                        Vote
                        </button> 
                        }

                        </div>
                  
                </div>
            ))}
        </Formik>
    </div>
  )
}
