/* eslint-disable react/jsx-pascal-case */
import React from 'react'
import { motion } from 'framer-motion'
import Become_an_event_organizer from './become_an_event_organizer'
import Showallevents from './showallevents'
import Footerpage from './footer'

export default function HomePage({event_signup_user}) {

    

    const homescreenvariants =  {

        initial:{
            opacity:0
        },
        animate:{
            opacity:1,
            transition:{
                delay:0,
                easeInOut:10,
                duration:2,
            }
        }

    }

  return (
    <div className='overflow-hidden w-full h-full flex flex-col items-center justify-center'>
      <motion.img
      variants={homescreenvariants}
      animate='animate'
      transition='transition'
      initial='initial'
      src={require('../assert/hscreen.png')}
      alt='hscreen'
      width={'1280px'}
      className='w-full xs:h-[300px] md:h-[600px] lg:h-auto'
      />
      


      <Become_an_event_organizer event_signup_user={event_signup_user} />

       <Showallevents/> 

      

      <Footerpage/>

    </div>
  )
}
