import React, { useEffect, useState } from 'react'
import { Link } from 'react-router-dom'
import {  motion } from 'framer-motion'
import Cookies from 'js-cookie'
export default function NavBar({event_signup_user}) {

    const variants = {

        initial:{
            x:'-40vw',
            y:'0vh',
        },
        animate:{
            x:'0vw',
            y:'0vh',
            transition:{
                delay:0,
                easeInOut:10,
                duration:1,
            }
        }

    }
    

const [user_active,setUseractive]=useState(null)
useEffect(()=>{
    const interval = setInterval(() => {
        const cookie_user =Cookies.get('user')
        if(cookie_user) return setUseractive(true)
    }, 1000);

    return ()=>clearInterval(interval)

},[user_active,setUseractive])
    

  return (
    <motion.div
    variants={variants}
    animate='animate'
    initial='initial'
     className='w-full h-[97px] bg-white shadow-xl lg:px-[54px] lg:pt-[2px] flex flex-row items-center justify-between'>
        
        <motion.img

        src={require('../assert/voteify.png')}
        alt='voteify'
        width={'150px'}
        height={'5px'}
        className='cursor-pointer xs:mx-auto lg:mx-0'
        onClick={()=>window.location.href='/'}
        />

        <div className='xs:hidden lg:flex flex-row items-center justify-between'>

            <Link onClick={()=>{

            if(window.location.pathname ==='/'){
            const footer = document.getElementById('footernav')
            footer.scrollIntoView({behavior:'smooth'})
            }else{
            window.location.href = '/'
            
            }
            }}>
            
            <p className='cursor-pointer text-new_green font-bold text-xl'>Contact</p>
            </Link>
            <p onClick={()=>{

                if(window.location.pathname ==='/'){
                const event_org = document.getElementById('event_org')
                event_org.scrollIntoView({behavior:'smooth'})
            }else{
                window.location.href = '/'
                const event_org = document.getElementById('event_org')
                event_org.scrollIntoView({behavior:'smooth'})
            }
            }} className='cursor-pointer lg:ml-[35px] lg:px-[24px] lg:py-[6px] text-new_green font-bold text-xl bg-bg_green rounded-[12px]'>How it goes</p>

            
            {
                user_active && event_signup_user &&
            <p onClick={()=>{
                const cookie_user =Cookies.get('user')
                if(cookie_user) { Cookies.remove('user')
                    window.location.href='/'
                } 
            }} className='cursor-pointer text-white px-4 py-2 rounded-[12px] bg-red-500 font-bold text-xl ml-4'>Logout</p>
            }

        </div>

        {
                user_active && event_signup_user &&<svg onClick={()=>{
                const cookie_user =Cookies.get('user')
                if(cookie_user) { Cookies.remove('user')
                    window.location.href='/'
                } 
            }} xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" strokeWidth={2.5} stroke="currentColor" className="size-6 xs:mr-8 xs:block lg:hidden text-red-500">
        <path strokeLinecap="round" strokeLinejoin="round" d="M15.75 9V5.25A2.25 2.25 0 0 0 13.5 3h-6a2.25 2.25 0 0 0-2.25 2.25v13.5A2.25 2.25 0 0 0 7.5 21h6a2.25 2.25 0 0 0 2.25-2.25V15M12 9l-3 3m0 0 3 3m-3-3h12.75" />
        </svg>
        }



    </motion.div>
  )
}
