import axios from 'axios'
import { Formik } from 'formik'
import React, { useState } from 'react'
import { useNavigate } from 'react-router-dom'
import Swal from 'sweetalert2'
import * as Yup from 'yup'
import { BeatLoader } from 'react-spinners'

export default function ForgottenPassword({setEvent_signup_user}) {

    const navigate = useNavigate()

    const EmailValidation =  Yup.object({
        email:Yup.string()
        .email('Invalid email address')
        .required('Email is required'),
    })
    const REACT_APP_web_link = process.env.REACT_APP_web_link

    const [loading,setLoading] = useState(false)

  return (
    <div className='w-full xs:px-[22px] md:px-[66px] flex flex-col items-center justify-center pt-12'>

        <Formik
        validationSchema={EmailValidation}
        initialValues={{
            email:''
        }}
        onSubmit={((values)=>{
            setEvent_signup_user(true)
            const new_values = {...values,forgotten_password:true}
            // localhost:8000/users/users/verify_email/?email
            setLoading(true)
            setTimeout(async() => {
                await axios.get(`${REACT_APP_web_link}users/verify_email/?email=${values.email}`)
                .then((response)=>{
                    navigate('/verification_code',{state:{user: new_values }})
                }).catch((e)=>{
                    console.log(e)
                    Swal.fire({
                        icon:'error',
                        title:'Email not available',
                        timer:3000,
                        timerProgressBar:true,
                        showConfirmButton:false
                    })
                }).finally(()=>{
                    setLoading(false)
                })
            }, 1000);

        })}
        >
            {((props)=>(
                <div className='xs:w-full md:w-[400px] bg-white shadow-lg rounded-[12px] flex flex-col items-center justify-center'>
                    <h2 className='text-center my-3'>Forgotten Password?</h2>
                    <p className='text-gray-400 text-md text-center'>Please enter your email below</p>
                    <input
                    placeholder='email'
                    type='email'
                     onChange={props.handleChange('email')} onBlur={props.handleBlur('email')}
                     className='px-[8px] py-[4px] outline-none mx-auto bg-input_field mt-[42px] xs:w-[310px] lg:w-[319px] h-[63px] rounded-[12px] shadow-lg'
                     />
                     <p className=' text-center w-full mt-2 text-red-500 font-bold'>{props.touched.email && props.errors.email}</p>
                    
                     {
                        loading?
                        <BeatLoader
                        color='#3EBF53'
                        size={32} 
                        />
                        :
                        <button onClick={props.handleSubmit} className='xs:mt-[40px] lg:mt-[34px] mb-10 font-bold text-white bg-new_green w-[186px] h-[53px] rounded-[12px] shadow-md'>
                            Send
                        </button> 
                    }
                </div>
            ))}
        </Formik>
      
    </div>
  )
}
