import { Formik } from 'formik'
import React, { useState } from 'react'
import { Link, useLocation } from 'react-router-dom'
import { BeatLoader } from 'react-spinners'
import * as Yup from 'yup'

import { useNavigate } from 'react-router-dom';
import Resizer from "react-image-file-resizer";
import axios from 'axios'
import Cookies from 'js-cookie'
import Swal from 'sweetalert2'
export default function UpdateEvent() {
  const [loading,setLoading]=useState('')
  const [imagePreviewUrl, setImagePreviewUrl] = useState(null)
    const [event_organizer,setEvent_organizer]=useState(null)
    
    // const [user,setUser]=useState(null)

    const base64ToFile = (base64String, filename) => {
      let arr = base64String.split(',');
      let mime = arr[0].match(/:(.*?);/)[1];
      let bstr = atob(arr[1]);
      let n = bstr.length;
      let u8arr = new Uint8Array(n);
    
      while (n--) {
        u8arr[n] = bstr.charCodeAt(n);
      }
    
      return new File([u8arr], filename, { type: mime });
    };


    const location = useLocation();

    const { user,event } = location.state || {}

    const [paid,setPaid]=useState(event?.paid)

    const navigate = useNavigate();

    const EventOrganizationValidationSchema = Yup.object({
    name_of_event:Yup.string()
    .required('Please enter the name of the event'),
    startDate: Yup.date()
    .required('Start date is required')
    .min(new Date(), 'Start date cannot be in the past')
    .nullable(),
    event_picture: Yup.mixed(),
    // .required('Please upload a picture for your event'),
    endDate: Yup.date()
    .required('End date is required')
    .nullable()
    .min(new Date(), 'End date cannot be in the past')
    .min(Yup.ref('startDate'), 'End date cannot before start date'),
    });
  return (
    <div className='xs:py-[32px] lg:py-[32px] w-full xs:px-[40px] lg:px-[191px] flex xs:flex-col lg:flex-row items-center justify-center h-full'>

        <img

        src={`${process.env.REACT_APP_link}${event?.event_picture}`}
        alt='createevent.png'
        width={'539px'}
        height={'545px'}
        className='cursor-pointer'
        />

        <div className='xs:-translate-x-5 md:-translate-x-3 xs:mt-[20px] md:mt-[40px] lg:mt-0 flex flex-col'>

              <div className='flex flex-row items-center justify-center xs:translate-x-4 md:translate-x-0 lg:translate-x-10'>

      <div className='flex flex-row items-center justify-center'>

      <svg xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" strokeWidth={1.5} stroke="currentColor" className="size-12 text-green-500">
      <path strokeLinecap="round" strokeLinejoin="round" d="M5.25 5.653c0-.856.917-1.398 1.667-.986l11.54 6.347a1.125 1.125 0 0 1 0 1.972l-11.54 6.347a1.125 1.125 0 0 1-1.667-.986V5.653Z" />
      </svg>
      {event?.from_date.toString().split('T')[0]}

      </div>

      <div className='flex flex-row items-center justify-center  mt-[20px] xs:-translate-y-2 md:-translate-y-2 lg:-translate-y-3 lg:ml-[60px]'>

      <svg xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" strokeWidth={1.5} stroke="currentColor" className="size-12 text-red-500">
      <path strokeLinecap="round" strokeLinejoin="round" d="M5.25 7.5A2.25 2.25 0 0 1 7.5 5.25h9a2.25 2.25 0 0 1 2.25 2.25v9a2.25 2.25 0 0 1-2.25 2.25h-9a2.25 2.25 0 0 1-2.25-2.25v-9Z" />
      </svg>

      {event?.to_date.toString().split('T')[0]}

      </div>





      </div>
          

        <Formik
        validationSchema={EventOrganizationValidationSchema}
        initialValues={{
            name_of_event:event?.name_of_event,
            startDate:event?.from_date,
            event_picture:event?.event_picture,
            endDate:event?.to_date
        }}
        onSubmit={((values)=>{
            // console.log(values?.event_picture)

            setLoading(true)

            setTimeout(async() => {
              const formData = new FormData()
              
              formData.append('name_of_event',values.name_of_event)
              formData.append('event_organizer',user?.id)
              formData.append('from_date',values.startDate)
              formData.append('to_date',values.endDate)
              if(values.event_picture instanceof File){
                formData.append('event_picture',values.event_picture)
              }
              formData.append('paid',paid)

              await axios.put(`${process.env.REACT_APP_web_link_event}${event.id}/`, formData, {
                headers: {
                  'Authorization': `Bearer ${user?.access}`,
                  'Content-Type': 'multipart/form-data', // This is important for FormData
                }
              })
                .then((response)=>{
                  Swal.fire({
                    icon:'success',
                    title:'Event Submitted',
                    timer:3000,
                    timerProgressBar:true,
                    showConfirmButton:false
                  }).then(()=>{
                    navigate('/event_home')
                  })
                }).catch((e)=>{
                  console.log(e)
                  Swal.fire({
                    icon:'error',
                    title:'An error occurred, please try again later',
                    timer:3000,
                    timerProgressBar:true,
                    showConfirmButton:false
                  })
                }).finally(()=>{
                  setLoading(false)
                })
              

            }, 1000);
        // alert(JSON.stringify(values))
       
        })}
        >
            {((props)=>(
                <div className='flex flex-col items-start justify-start xs:my-[40px] lg:my-0 xs:ml-[20px] xs:mr-[20px] lg:mr-0 lg:ml-[138px] xs:px-[20px] lg:px-[0px] xs:h-[auto] lg:h-auto xs:w-full lg:w-[418px] h-full shadow-lg rounded-[12px]'>

                    <h2 className='text-center w-full font-bold text-2xl my-4'>Update an Event</h2>


                    <input
                    placeholder='name'
                     onChange={props.handleChange('name_of_event')} onBlur={props.handleBlur('name_of_event')}
                     value={props.values.name_of_event}
                     className='px-[8px] py-[4px] outline-none mx-auto bg-input_field mt-[42px] xs:w-full lg:w-[319px] h-[63px] rounded-[12px] shadow-lg'
                     />
                     <p className=' text-center w-full mt-2 text-red-500 font-bold'>{props.touched.name_of_event && props.errors.name_of_event}</p>


                     {imagePreviewUrl &&
                    <div className='w-full flex flex-row items-center justify-center mt-10 mb-1'>
                     <img 
                     src={imagePreviewUrl}
                     alt='event'
                     className='w-[100px] h-[50px]'
                     />
                     </div>
                     }

                     <input
                     id='event_picture'
                    placeholder='picture'
                    accept='image/*'
                    onChange={(event) => {

                        Resizer.imageFileResizer(
                            event.target.files[0],
                            150,
                            150,
                            "JPEG",
                            100,
                            0,
                            (uri) => {
                              // console.log(event.target.files[0].name);
                              const file = base64ToFile(uri, `${event.target.files[0].name}`);
                              const previewUrl = URL.createObjectURL(file);
                              setImagePreviewUrl(previewUrl);
                              props.setFieldValue('event_picture', event.target.files[0]);
                              // props.setFieldValue('event_picture', file);
                              // props.setFieldValue('event_picture', uri);
                            },
                            "base64",
                            100,
                            100
                          );

                        
                      }}
                    onBlur={props.handleBlur('event_picture')}
                     className='px-[8px] py-[4px] outline-none mx-auto bg-input_field mt-[42px] xs:w-full lg:w-[319px] h-[63px] rounded-[12px] shadow-lg hidden'
                     type='file'
                     />
                     <label htmlFor='event_picture' className='px-[8px] py-[4px] outline-none mx-auto bg-input_field mt-[42px] xs:w-full lg:w-[319px] cursor-pointer h-[43px] rounded-[12px] shadow-lg text-center'>
                        Event Picture
                     </label>
                     <p className=' text-center w-full mt-2 text-red-500 font-bold'>{props.touched.event_picture && props.errors.event_picture}</p>


                     <div className='px-[50px] py-[4px] w-full flex flex-row items-center justify-center'>

                     <label className=' mt-10'>From Date</label>
                     <input
                    placeholder='startDate'
                     onChange={props.handleChange('startDate')} onBlur={props.handleBlur('startDate')}
                     className='px-[8px] py-[4px] outline-none mx-auto bg-input_field mt-[42px] xs:w-full lg:w-[219px] h-[63px] rounded-[12px] shadow-lg'
                     type='date'
                    //  value={props.values.startDate}
                    value={props.values.startDate}
                     />
                     </div>

                     <p className=' text-center w-full mt-2 text-red-500 font-bold'>{props.touched.startDate && props.errors.startDate}</p>
                     
                     <div className='px-[50px] py-[4px] w-full flex flex-row items-center justify-center'>

                     <label className=' mt-10'>To Date</label>
                     <input
                    placeholder='endDate'
                     onChange={props.handleChange('endDate')} onBlur={props.handleBlur('endDate')}
                     className='px-[8px] py-[4px] outline-none mx-auto bg-input_field mt-[42px] xs:w-full lg:w-[219px] h-[63px] rounded-[12px] shadow-lg'
                     type='date'
                     value={props.values.endDate}
                     />
                     </div>


                     
                     <p className=' text-center w-full mt-2 text-red-500 font-bold'>{props.touched.endDate && props.errors.endDate}</p>
                    
                     

                     <div className='relative my-4 px-[50px] py-[4px] flex flex-row items-start justify-center'>

                      <p className='mr-[10px]'>Paid Event</p>

                      <div className='relative w-[100px] bg-gray-300 rounded-3xl h-[40px]'></div>

                      <div onClick={()=>setPaid(!paid)} className={`absolute  cursor-pointer transition-all duration-300 ease-in-out ${!paid?'left-[133px] top-[4px] w-[40px] h-[40px] bg-red-500':'left-[196px] top-[4px] w-[40px] h-[40px] bg-green-500'} rounded-full shadow-xl`}></div>

                      </div>

                    <div className='w-full flex flex-row items-center justify-center'>
                        {
                        loading?
                        <BeatLoader
                        color='#3EBF53'
                        size={32} 
                        />
                        :
                        <button onClick={props.handleSubmit} className='xs:mt-[40px] lg:mt-[34px] font-bold text-white bg-new_green w-[186px] h-[53px] rounded-[12px] shadow-md mb-[20px]'>
                        Update Event
                        </button> 
                        }

                        </div>
                  
                </div>
            ))}
        </Formik>
        </div>
    </div>
  )
}
